.contacts{
  padding: 1.56em 0 3.125em;
  &__inner{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 22.375em) 20.5em;
    gap: 1.56em 1.875em;
  }
  &__items{
    @include grid100gap(1.875em);
  }
}

.contacts-form{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .9em));
  gap: 1.81em;
  &__bottom{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 15.0625em;
    padding: 0;
    font-weight: 700;
    margin-right: 1.875em;
  }
}

._full{
  grid-column: 1/-1;
}

.contact-card{
  padding: 1.56em;
  border-radius: 0.1875em;
  background: #F9F9F9;
  &__item{
    display: flex;
    margin-top: .375em;
  }
  &__icon{
    @include center;
    width: 1em;
    height: 1.3125em;
    flex-shrink: 0;
    margin-right: .44em;
    margin-top: .125em;
    img{
      width: 100%;
      display: block;
    }
    &._type2{
      width: 1.25em;
      margin-top: .25em;
    }
  }
  &__phone{
    font-weight: 500;
  }
  &__text{
    margin-top: .375em;
    color: #404748;
  }
  &__mail{
    font-weight: 500;
    text-decoration: underline;
    color: $primary;
  }
  .socials2{
    margin-top: .66em;
  }
}

.socials2{
  display: flex;
  align-items: center;
  .socials__item{
    background: #fff;
    @include lastM(0 .94em 0 0);
    &:hover{
      border-color: rgba(255, 255, 255, 0.80);
      background: rgba($primary,.1);
    }
    img{
      max-width: 1.1875em;
    }
  }
}

.contacts-map{
  height: 37.5em;
}

.contacts2{
  padding: 3.125em 0 5em;
}

.contact-card2{
  padding: 1.56em;
  border-radius: .1875em;
  border: $border2;
  &__text{
    line-height: 167%;
    p{
      line-height: inherit;
    }
  }
}

.m-form{
  position: relative;
  &._success{
    .m-form__inner{
      opacity: 0;
      pointer-events: none;
    }
    .thank-form{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.thank-form{
  position: absolute;
  @include center;
  flex-direction: column;
  text-align: center;
  padding: 1em;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  box-shadow: 0 .25em 2.125em 0 rgba(0, 0, 0, 0.06);
  opacity: 0;
  pointer-events: none;
  &__img{
    width: 4.6875em;
    display: block;
    margin-bottom: .94em;
  }
  &__text{
    margin-top: .94em;
    max-width: 33.0625em;
    color: $textGray;
    .text17{
      line-height: 164%;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__inner{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .contacts-form{
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    &__btn{
      margin-right: 1.56em;
    }
  }

  .contacts-map{
    height: 22.5em;
  }

  .contacts2{
    padding: 3.125em 0;
  }

  .contact-card2{
    padding: 1.25em;
  }

  .thank-form{
    &__img{
      width: 3.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .contacts-form{
    grid-template-columns: 100%;
    &__bottom{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 1.56em;
    }
  }

  .contacts-map{
    height: 12.55em;
  }

  .contacts2{
    &__inner{
      gap: 1.25em;
    }
  }
}
