.offers{
  &__grid{
    margin-top: 2.5em;
  }
}

.offer-card{
  padding: 3.19em 2.5em;
  color: #fff;
  border-radius: 0.3125em;
  background-size: cover;
  background-position: center;
  .m-badge{
    margin-bottom: .875em;
    text-transform: unset;
    font-family: $font2;
    padding: 0 .75em;
    height: 2.0625em;
    span{
      font-size: .875em;
      transform: unset;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 19.875em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: .875em;
  }
  &__link{
    margin-top: .875em;
    height: 3.4375em;
    padding: 0 1.44em;
  }
}

.manufactures{
  padding-top: 0;
}

.manufactures-slider{
  margin-top: 2.5em;
  @media screen and (min-width: 993px){
    .swiper{
      &-nav{
        display: none;
      }
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
        gap: 1.875em;
      }
      &-slide{
        width: 100%;
      }
    }
  }
}

.manufacture-card{
  @include center;
  height: 4.9375em;
  border-radius: 0.1875em;
  border: $border;
  padding: .625em;
  &:hover{
    border-color: $primary;
  }
  img{
    max-width: 100%;
    max-height: 100%;
  }
}

.about{
  padding-top: 2em;
  &__inner{
    display: grid;
    grid-template-columns: 50.6875em calc(100% - 50.6875em);
  }
  &__content{
    padding: 3.125em 0;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .text{
    margin-top: 2.875em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__link{

  }
}

.advantages{
  padding: 1.25em 0;
  border-top: 1px solid #F1F1F6;
  border-bottom: 1px solid #F1F1F6;
  background: #FBFBFC;
}

.advantage-item{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 3.5625em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: .94em;
    @include grid100gap(.375em);
  }
  &__desc{
    color: #535657;
    line-height: 167%;
    p{
      line-height: inherit;
    }
    ul{
      line-height: inherit;
      padding-left: .6em;
      li{
        line-height: inherit;
        padding-left: .8em;
        position: relative;
        &:before{
          content: '•';
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }
}

.news{
  overflow: hidden;
}

.news-card{
  &__img{
    border-radius: .1875em;
    @include imgCover(53.5%);
  }
  &__content{
    @include grid100gap(.68em);
    margin-top: 1em;
  }
  &__title{
    color: #2C333D;
  }
  &__desc{
    color: #393939;
  }
}


@media screen and (max-width: 992px){
  .offers{
    &__grid{
      margin-top: 1.875em;
    }
  }

  .offer-card{
    min-height: 21.25em;
    display: flex;
    align-items: flex-end;
    padding: 1.25em;
    &__content{
      max-width: 18.5em;
    }
    .m-badge{
      margin-bottom: .625em;
    }
    .text34{
      max-width: 11em;
    }
    &__desc,
    &__link{
      margin-top: .625em;
    }
  }

  .manufactures{
    overflow: hidden;
    padding-top: 1.875em;
  }

  .manufactures-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width: 9.0625em;
        padding-right: .9375em;
      }
    }
  }

  .manufacture-card{
    height: 3.125em;
  }

  .about{
    padding: 1.56em 0 3.125em;
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__content{
      padding: 0;
    }
    &__img{
      padding: 0 3.31em;
    }
    .text{
      margin-top: 1.875em;
    }
  }

  .advantages{
    padding: 1.56em 0;
  }

  .news-slider{
    .swiper-slide{
      width: 50%;
    }
  }
}


@media screen and (max-width: 576px){
  .offers,
  .manufactures{
    &__inner{
      position: relative;
      padding-bottom: 4.375em;
    }
    .m-section__link{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2.5em;
      ._view2{
        display: none;
      }
      ._view1{
        display: block;
      }
    }
  }

  .offers{
    padding: 3.125em 0;
  }

  .manufactures{
    padding: 1.56em 0;
  }

  .about{
    &__inner{
      gap: .94em;
    }
    &__nav{
      margin-top: .9375em;
      justify-content: center;
    }
    &__img{
      padding: 0;
    }
  }

  .advantages{
    padding: 1.25em 0;
  }

  .advantage-item{
    &__content{
      padding-top: .625em;
    }
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .news-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .news-card{
    &__title{
      font-size: 1.0625em;
    }
  }
}
