.has-reg{
  padding: .81em 1.25em;
  border-radius: 0.1875em;
  border: 1px dashed #E8EDF2;
  background: #FAFBFC;
  display: flex;
  align-items: center;
  &__btn{
    padding: 0 1em;
    height: 2.3125em;
    text-transform: uppercase;
    font-weight: 700;
    margin-left: .56em;
    border-width: 2px;
    span{
      font-size: .75em;
    }
  }
}

.order-form{
  @include grid100gap(1.06em);
  margin-top: 2.5em;
}

.order-step{
  padding-left: 3.94em;
  padding-bottom: 1.44em;
  position: relative;
  &:last-child{
    &:before{
      display: none;
    }
  }
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 1.5em;
    opacity: 0.1;
    background: $blue;
    width: .125em;
    height: calc(100% - 4.125em);
  }
  &__top{
    display: flex;
    align-items: center;
    margin-left: -3.94em;
  }
  &__num{
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    border: 2px solid $blue;
    @include center;
    font-weight: 700;
    color: $blue;
    margin-right: .81em;
    flex-shrink: 0;
    span{
      font-size: 1.125em;
    }
  }
  &__title{
    color: $blue;
  }
  &__type{
    display: flex;
    align-items: center;
    margin: 1.875em 0;
    .m-radio{
      @include lastM(0 1.75em 0 0);
    }
  }
  &__comment{
    margin-top: 1.875em;
    textarea{
      height: 5.6em;
    }
  }
  &__auth{
    margin-top: 1.25em;
  }
}

.user-type{
  display: none;
  &._active{
    display: block;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
  }
}

.delivery-type{
  display: flex;
  flex-wrap: wrap;
  margin: .625em 0 1.875em;
  &__item{
    margin: 1.25em 1.25em 0 0;
    min-width: 16.5625em;
    &-inner{
      display: flex;
      align-items: center;
      padding: .5em 1.25em;
      min-height: 4.75em;
      border-radius: 0.1875em;
      border: $border2;
      outline: 1px solid transparent !important;
      transition: all .3s;
    }
    img{
      width: 2.75em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    input:hover~.delivery-type__item-inner{
      border-color: $primary;
    }
    input:checked~.delivery-type__item-inner{
      border-color: $primary;
      outline-color: $primary !important;
    }
  }
}

.delivery-way{
  display: none;
  &._active{
    display: block;
  }
  &__address{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__map-btn{
    margin: .44em 0 0 2.125em;
  }
}

.delivery-addresses{
  margin-top: 1.25em;
  &:first-child{
    margin-top: 0;
  }
  &__items{
    margin-top: .81em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.address-field,
.duration-field{
  &__inner{
    min-height: 100%;
    padding: 1em 1.25em;
    border-radius: 0.1875em;
    border: $border2;
  }
  .radio-icon{
    margin-right: .625em;
  }
}

.address-field{
  input:checked{
    &~.address-field__inner{
      border-color: $blue;
      .radio-icon{
        border: none;
        background-size: 100% auto;
      }
    }
  }
  &__title{
    text-transform: uppercase;
    font-size: .875em;
    font-weight: 700;
    line-height: 140%;
    color: $blue;
  }
  &__wrap{
    margin-top: .625em;
    display: flex;
  }
  &__desc{
    font-size: .9375em;
    line-height: 140%;
  }
}

.radio-icon{
  border: 1px solid #DADEDF;
  width: 1.25em;
  height: 1.25em;
  @include bgImgSize(0);
  border-radius: 50%;
  background-image: url(../img/radio.svg);
  flex-shrink: 0;
}

.duration{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
  gap: 1.25em;
  margin-top: 1.875em;
}

.duration-field{
  input:checked{
    &~.duration-field__inner{
      border-color: $blue;
      .radio-icon{
        border: none;
        background-size: 100% auto;
      }
    }
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__desc{
    font-size: 1em;
    line-height: 140%;
    font-weight: 500;
    color: $blue;
  }
}

.order-pay{
  margin-top: .625em;
  display: flex;
  flex-wrap: wrap;
}

.pay-field{
  margin: 1.25em 1.25em 0 0;
  width: 16.4375em;
  .duration-field__inner{
    align-items: unset;
  }
}


@media screen and (max-width: 992px){
  .order-form{
    margin-top: 1.56em;
  }

  .order-step{
    padding-left: 2.875em;
    padding-bottom: 1.81em;
    &:before{
      left: 1.0625em;
      height: calc(100% - 3.1875em);
    }
    &__top{
      margin-left: -2.875em;
    }
    &__num{
      width: 2.25em;
      height: 2.25em;
      margin-right: .625em;
      span{
        font-size: .9375em;
      }
    }
    &__title{
      font-size: 1.0625em;
    }
    &__type{
      margin: 1.625em 0 1.56em;
      .m-radio{
        @include lastM(0 .9375em 0 0);
      }
    }
    &__comment{
      margin-top: 1.56em;
    }
    &__auth{
      margin-top: 1em;
    }
  }

  .delivery-type{
    margin: 1.56em 0;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.56em * 2 / 3));
    gap: 1.56em;
    &__item{
      margin: 0;
      width: 100%;
      min-width: unset;
      &-inner{
        padding: .5em .94em;
      }
      img{
        width: 2.25em;
      }
      .text16{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }

  .delivery-way{
    &__map-btn{
      margin-top: .31em;
    }
  }

  .delivery-addresses{
    margin-top: 1.56em;
    &__items{
      margin-top: .44em;
      grid-template-columns: repeat(2,calc(50% - .78em));
      gap: 1.56em;
    }
  }

  .address-field{
    &__inner{
      padding: .94em;
      min-height: 6.25em;
    }
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .order-pay,
  .duration{
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
    margin-top: 1.56em;
  }

  .duration-field{
    &__inner{
      padding: .94em;
    }
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .order-pay{
    display: grid;
  }

  .pay-field{
    margin: 0;
    width: 100%;
    br{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .has-reg{
    padding: .81em .94em;
    display: block;
    &__btn{
      margin: .94em 0 0;
      width: 100%;
    }
  }

  .order-step{
    padding: 0;
    &:before{
      display: none;
    }
    &__top{
      margin-left: 0;
    }
    &__type{
      margin: .9375em 0;
      display: block;
      .m-radio{
        @include lastM(0 0 .9375em 0);
      }
    }
    &__comment,
    &__auth{
      margin-top: .94em;
    }
  }

  .user-type{
    &__fields{
      grid-template-columns: 100%;
      gap: .9375em;
    }
  }

  .delivery-type{
    margin: .94em 0;
    grid-template-columns: 100%;
    gap: .94em;
    &__item{
      &-inner{
        min-height: 4.125em;
      }
    }
  }

  .delivery-addresses{
    margin-top: .94em;
    &__items{
      grid-template-columns: 100%;
      gap: .94em;
    }
  }

  .order-pay,
  .duration{
    grid-template-columns: 100%;
    gap: .94em;
    margin-top: .94em;
  }
}
