.delivery{
  padding: 1.56em 0 5em;
  overflow: hidden;
  &__desc{
    margin-top: 1em;
  }
}

.delivery-group{
  margin-top: 2.19em;
  @include grid100gap(.94em);
  width: calc(100% + .94em);
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.94em;
    margin-bottom: .94em;
  }
}

.pay-item,
.delivery-item{
  @include center;
  padding: .9375em;
  height: 3.6875em;
  border-radius: 0.1875em;
  border: $border2;
  margin: .94em .94em 0 0;
  img{
    max-height: 100%;
    max-width: 100%;
    display: block;
  }
}

.m-table{
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,td{
    padding: .25em .78em;
    text-align: left;
    &:first-child{
      padding-left: 1.56em;
    }
    &:last-child{
      padding-right: 1.56em;
    }
  }
  th{
    height: 3.5625em;
    background-color: $lightGray;
    font-weight: 500;
  }
  td{
    height: 3.5em;
  }
  tbody{
    tr:nth-child(even){
      background: #FBFBFB;
    }
  }
  &__th{
    font-size: 1.125em;
    line-height: 150%;
  }
  &__td{
    font-size: .9375em;
    line-height: 173%;
  }
  &__price{
    font-size: 1.0625em;
    line-height: 153%;
    font-weight: 500;
    color: $blue;
  }
}

.pay-item{
  height: 4.75em;
}

.conf{
  padding: 1.56em 0 5em;
  .article{
    margin-top: 1.875em;
    font-weight: 400;
    a{
      color: $primary;
      font-weight: 700;
      text-decoration: underline;
    }
  }
  &__hint{
    padding: 1.25em 1.56em;
    border-radius: 0.5em;
    background: #F8F8F8;
    margin-top: 1.56em;
    .article{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .delivery{
    padding-bottom: 3.125em;
  }

  .delivery-group{
    margin-top: 1.56em;
  }

  .m-table{
    th{
      height: 3.0625em;
    }
    &__th{
      font-size: .875em;
      line-height: 135%;
    }
    &__td{
      font-size: .875em;
      line-height: 143%;
    }
    &__price{
      font-size: .9375em;
      line-height: 173%;
    }
  }

  .conf{
    .article{
      margin-top: 1.56em;
    }
    &__hint{
      padding: 1.56em;
    }
  }
}


@media screen and (max-width: 576px){
  .m-table{
    @include fullScreenMob;
    overflow-x: auto;
    table{
      min-width: 28em;
    }
  }

  .delivery-item{
    height: 3.125em;
  }

  .pay-item{
    height: 3.4375em;
    margin-right: .625em;
    max-width: calc(25% - .625em);
  }

  .conf{
    &__hint{
      padding: 1.25em;
    }
  }
}
