.error404{
  padding: 1.56em 0 5em;
  background-image: url(../img/star2.svg);
  background-repeat: no-repeat;
  background-position: right 17.38em bottom 0;
  background-size: 48.625em auto;
  &__inner{
    display: grid;
    grid-template-columns: 41.8125em calc(100% - 43em);
    justify-content: space-between;
  }
  &__desc{
    margin-top: 1.56em;
    max-width: 39.5em;
  }
  .search-field{
    margin-top: 1.12em;
  }
  &__links{
    margin-top: 2.56em;
    display: flex;
    justify-content: space-between;
    max-width: 27em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    height: 3.8125em;
    text-transform: uppercase;
    font-weight: 700;
    min-width: 16.3125em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      max-width: 31.125em;
      margin: 0 auto;
    }
  }
}

.search-field2{
  input{
    border-width: .1875em;
    border-color: $blue;
  }
  .search-field__btn{
    width: 3.2em;
    height: 100%;
    background: $blue;
    right: 0;
    top: 0;
    transform: unset;
    border-radius: 0 .1875em .1875em 0;
  }
}

.links-group{
  &__title{
    opacity: 0.4;
    font-size: 0.875em;
    font-weight: 700;
    line-height: 140%;
    text-transform: uppercase;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .625em;
  }
  &__item{
    @include lastM(0 0 .625em);
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    background-position: right -5em bottom 0;
    background-size: 33.625em auto;
    &__inner{
      grid-template-columns: 26.5em calc(100% - 26.5em);
    }
    &__desc{
      margin-top: 1em;
      .text15{
        line-height: 173%;
      }
    }
    .search-field{
      margin-top: 1.75em;
    }
    &__links{
      margin-top: 1.56em;
      display: grid;
      grid-template-columns: 1fr .9fr;
      gap: 1.56em;
    }
    &__nav{
      margin-top: 1.56em;
    }
  }
}

@media screen and (max-width: 576px){
  .error404{
    background-size: 100% auto;
    background-position: center bottom 8em;
    &__inner{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__links{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
