.catalog-item{
  padding: 1.56em 0;
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 35.375em - 1.875em) 35.375em;
    gap: 1.875em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 6.5em calc(100% - 6.5em - .94em);
  gap: .94em;
  &__img{
    display: block;
    @include imgCover(76.2%);
    padding: 0;
    height: 100%;
    transition: all .3s;
    img{
      transition: all .3s;
    }
  }
  &__small{
    position: relative;
    height: 37.6875em;
    .swiper{
      height: calc(100% + .94em);
      &-slide{
        height: calc(100% / 6);
        padding-bottom: .94em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &-thumb-active .gallery__img{
          border-color: $black;
          img{
            opacity: .4;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      border: $border;
      &:hover{
        border-color: $black;
      }
    }
  }
  &__big{
    width: 100%;
    height: 37.6875em;
    position: relative;
    border: $border;
    .swiper{
      height: 100%;
    }
  }
  .swiper-pagination{
    bottom: 1.875em;
    height: unset;
    width: 100%;
    justify-content: center;
    top: unset !important;
    transform: unset !important;
  }
  .product-card__badges{
    top: 1.25em;
    left: 1.25em;
  }
  .m-badge{
    height: 2.125em;
    padding: 0 .75em;
    display: flex;
    span{
      font-size: 0.9375em;
    }
  }
}

.catalog-info{
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 16.9em - 1.56em) 16.9em;
    gap: 1.56em;
    margin-top: 1.25em;
  }
  &__reviews{
    display: flex;
    align-items: center;
    margin-top: .625em;
    &-btn{
      margin-left: .625em;
      border-bottom: 1px dotted $black;
      span{
        font-size: 1em;
        line-height: 140%;
      }
      &:hover{
        border-color: $primary;
        color: $primary;
      }
    }
  }
  &__available{
    display: flex;
    align-items: center;
    margin-top: 1.56em;
    &-info{
      display: flex;
      align-items: center;
      color: #1B9665;
      margin-left: 1.25em;
      img{
        width: 1.0625em;
        margin-right: .5em;
      }
    }
  }
  .product-prices{
    margin-top: 3.06em;
    &__item{
      @include lastM(0 .625em 0 0);
    }
    &__old{
      font-size: 1.25em;
    }
    &__current{
      font-size: 1.875em;
    }
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: 1.56em;
    .m-btn{
      height: 3.9375em;
      padding: 0;
      font-weight: 700;
      border-radius: 0.1875em;
      text-transform: uppercase;
      img{
        width: 1.56em;
        &._view2{
          width: 1.125em;
        }
      }
    }
    .m-btn-red-outline{
      svg{
        margin-left: .94em;
        width: 1.5em;
      }
      &:hover{
        path{
          stroke: #fff;
        }
      }
    }
  }
  &__bottom{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
  }
  .favourite-btn,
  .compare-btn{
    justify-content: unset;
    width: auto;
    font-weight: 500;
    color: #2C2F32;
    &:hover{
      color: $primary;
    }
    &__icon{
      width: 3em;
      height: 3em;
      border-radius: 0.1875em;
      border: $border;
      flex-shrink: 0;
      @include center;
      margin-right: .94em;
      svg{
        width: 1.375em;
      }
    }
  }
  .compare-btn{
    &__icon{
      border-color: $lightGray;
      background-color: $lightGray;
    }
  }
}

.articul{
  display: flex;
  align-items: center;
  &__copy{
    margin-left: .44em;
  }
  .text16{
    line-height: 140%;
  }
}

.copy-btn{
  width: 1.25em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $black;
    }
  }
}

.product-advantages{
  @include grid100gap(.94em);
  padding: 1.25em 1.56em;
  border-radius: 0.1875em;
  border: $border2;
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.doc-item{
  display: flex;
  align-items: center;
  color: #2C2F32;
  &__icon{
    background-color: $lightGray;
    width: 3em;
    height: 3em;
    border-radius: 0.1875em;
    flex-shrink: 0;
    @include center;
    margin-right: .94em;
    img{
      width: 1.5em;
    }
  }
  &__title{
    font-weight: 500;
    line-height: 140%;
  }
  &__link{
    color: #808487;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__top{
      grid-template-columns: 19.125em calc(100% - 19.125em - 1.56em);
      gap: 1.56em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: .9375em;
    &__big{
      height: 17.5em;
    }
    &__small{
      order: 2;
      height: auto;
      position: relative;
      &:after{
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        background: linear-gradient(270deg, #FFF 31.37%, rgba(255, 255, 255, 0.81) 56.6%, rgba(255, 255, 255, 0.60) 69.79%, rgba(255, 255, 255, 0.00) 92.16%);
        width: 3.1875em;
        z-index: 3;
        opacity: .7;
        pointer-events: none;
      }
      .swiper{
        height: auto;
        width: 100%;
        &-slide{
          padding: 0 .625em 0 0;
          height: 4.25em;
          width: 5.625em;
        }
      }
    }
    .product-card__badges{
      top: .94em;
      left: .94em;
    }
    .swiper-pagination{
      bottom: .9375em;
    }
  }

  .catalog-info{
    &__wrap{
      grid-template-columns: calc(100% - 11.3125em - 1.56em) 11.3125em;
    }
    &__reviews{
      &-btn{
        font-size: .875em;
        font-weight: 700;
      }
    }
    &__available{
      margin-top: 1.25em;
      display: block;
      .cartcalc{
        width: 100%;
      }
      &-info{
        margin: .625em 0 0;
        font-weight: 700;
      }
    }
    .product-prices{
      margin-top: 1.56em;
      white-space: nowrap;
      &__old{
        font-size: 1em;
      }
      &__current{
        font-size: 1.5625em;
      }
    }
    &__nav{
      grid-template-columns: 100%;
      gap: .94em;
      .m-btn{
        height: 3.4375em;
      }
    }
    &__bottom{
      margin-top: 1.56em;
      grid-template-columns: 100%;
      gap: .94em;
    }
    .favourite-btn,
    .compare-btn{
      &__icon{
        width: 2.5em;
        height: 2.5em;
        svg{
          width: 1.125em;
        }
      }
    }
  }

  .product-advantages{
    padding: .94em;
    &__item{
      img{
        width: 1.5em;
      }
    }
  }

  .articul{
    &__copy{
      width: 1em;
    }
  }

  .doc-item{
    &__icon{
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1.25em;
      }
    }
    &__title{
      font-size: 0.875em;
      line-height: 143%;
    }
    &__link{
      font-weight: 700;
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px) {
  .catalog-item {
    &__top {
      grid-template-columns: 100%;
    }
  }

  .catalog-info{
    &__wrap{
      grid-template-columns: 100%;
    }
    &__available{
      display: flex;
      &-info{
        margin: 0 0 0 .94em;
      }
      .cartcalc{
        width: 9.875em;
        height: 2.5em;
      }
    }
  }
}
