.manufactures-page{
  padding: 1.56em 0 7.5em;
}

.manufactures-group{
  margin-top: 2.5em;
  @include grid100gap(1.44em);
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
    gap: 1.875em;
    &._gray{
      @media screen and (min-width: 993px){
        .manufacture-card{
          outline: 1px solid transparent !important;
          &:not(:hover){
            img{
              opacity: .4;
              filter: grayscale(1);
            }
          }
          &:hover{
            border-color: $primary !important;
          }
          img{
            transition: all .3s;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .manufactures-page{
    padding: 1.56em 0 3.125em;
  }

  .manufactures-group{
    margin-top: 1.5625em;
    gap: 1.25em;
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
      gap: 1.25em;
    }
    .manufacture-card{
      height: 5em;
    }
  }
}


@media screen and (max-width: 576px){
  .manufactures-group{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }
}
