.orders{
  &__top{
    display: flex;
  }
  &__num{
    margin-left: .3125em;
    margin-top: .19em;
    opacity: 0.5;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__items{
    @include grid100gap(1.875em);
  }
  .pagination{
    margin-top: 1.25em;
    &__items{
      justify-content: unset;
    }
    &__item{
      &:first-child{
        margin-left: 0;
      }
    }
  }
}

.order-card{
  border: $border2;
  &__top{
    padding: 1.56em;
    display: flex;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 1.31em;
    height: 2.1875em;
    border-radius: 0.3125em;
    border: 2px solid $yellow;
    color: $textGray;
    font-weight: 500;
    &._green{
      border-color: #1B9665;
    }
    span{
      font-size: .9375em;
      line-height: 140%;
    }
  }
  &__content{
    padding: 1.56em;
    border-top: $border2;
  }
  .order-thank__items{
    max-width: 54.9375em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__more{
    color: #515A5C;
    display: flex;
    align-items: center;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .5em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &:not(._show-all){
    .order-thank__item:nth-child(3)~.order-thank__item{
      display: none;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    padding: 0 3.75em;
  }
  &__footer{
    padding: 1.56em;
    border-top: $border2;
    @media screen and (min-width: 993px){
      &._type{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .order-card__detail{
          max-width: 50em;
          flex-grow: 1;
        }
      }
    }
  }
  &__detail{
    @include grid100gap(.94em);
  }
}

.order-item{
  .order-thank__item{
    display: grid !important;
  }
  &__nav{
    margin-left: 1em;
  }
  &__btn{
    height: 2.9375em;
    span{
      font-size: .6875em;
    }
  }
}

.order-track{
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
}

.tracking{
  display: flex;
  justify-content: space-between;
}

.tracking-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgba($blue,.6);
  width: 6.25em;
  position: relative;
  &._active{
    color: $blue;
    .tracking-item__icon{
      background: $blue;
      img{
        display: block;
      }
      &:after{
        display: none;
      }
    }
  }
  &._finished{
    color: $blue;
    &:after{
      background: $blue;
    }
    .tracking-item__icon{
      &:after{
        background: $yellow;
        border: none;
      }
    }
  }
  &:last-child{
    &:after{
      display: none;
    }
  }
  &:after{
    content: '';
    position: absolute;
    width: 9.375em;
    height: 0.125em;
    left: 4.88em;
    top: 1.125em;
    background: #E7ECED;
  }
  &__icon{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    @include center;
    position: relative;
    margin-bottom: .625em;
    &:after{
      content: '';
      @include centerAbsolute;
      width: .9375em;
      height: .9375em;
      border: 2px solid $blue;
      border-radius: 50%;
    }
    img{
      width: 1.25em;
      display: none;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 140%;
    font-weight: 500;
    white-space: nowrap;
  }
}

.track-hint{
  padding: .94em 1.56em;
  border-radius: 0.1875em;
  border: 1px dashed #99B8C0;
  background: #FBFCFD;
  margin-top: 1.25em;
  @include grid100gap(.94em);
  &__btn{
    color: $blue;
    text-decoration: underline;
    &:hover{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .orders{
    &__num{
      margin-left: .06em;
      margin-top: -.31em;
      span{
        font-size: .875em;
        line-height: 114%;
      }
    }
    &__items{
      gap: 1.56em;
    }
    .pagination{
      margin-top: .3125em;
    }
  }

  .order-track{
    &__nav{
      margin-top: 0;
    }
  }

  .tracking-item{
    &:after{
      width: 5.23em;
      left: 4.56em;
    }
    &__icon{
      width: 2.25em;
      height: 2.25em;
    }
  }

  .track-hint{
    margin-top: -.625em;
    gap: .625em;
    .text18{
      line-height: 135%;
    }
    &__desc{
      line-height: 143%;
    }
  }

  .order-item{
    &__nav{
      display: flex;
      margin: 1.56em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .orders{
    &__items{
      gap: .94em;
    }
    .pagination{
      &__items{
        justify-content: center;
      }
      &__item{
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }

  .order-card{
    &__top,
    &__content{
      padding: .94em;
    }
    .order-thank__items{
      gap: .94em;
      margin-top: .94em;
    }
    .order-thank__item{
      padding-top: .94em;
      border-top: $border2;
      &:first-child{
        padding-top: 0;
        border-top: none;
      }
    }
    &__nav{
      margin-top: .94em;
    }
    &__more{
      span{
        font-size: .875em;
      }
    }
    &__bottom{
      display: block;
    }
    &__btn{
      padding: 0;
      width: 100%;
      margin-top: .94em;
    }
    &__footer{
      padding: .94em;
    }
  }

  .order-track{
    &__nav{
      margin-top: .625em;
      .m-btn{
        width: 100%;
      }
    }
  }

  .tracking{
    @include grid100gap(1.5em);
  }

  .tracking-item{
    flex-direction: row;
    width: 100%;
    &:after{
      height: 1em;
      width: .125em;
      top: 100%;
      left: 1.0625em;
      margin-top: .25em;
    }
    &__icon{
      margin: 0 .625em 0 0;
      flex-shrink: 0;
    }
  }

  .track-hint{
    margin-top: 0;
    padding: .94em;
  }

  .order-item{
    &__btn{
      width: 100%;
    }
  }
}
