.new-appeal{
  margin-top: 2.5em;
  &__types{
    margin-top: 1.875em;
    max-width: 58.125em;
  }
  &__hidden{
    display: none;
  }
}

.appeal-type{
  color: $blue;
  &__inner{
    border-radius: 0.1875em;
    border: $border2;
    outline: 1px solid transparent !important;
    background-color: #fff;
    box-shadow: 0 .25em 1.6875em 0 rgba(0, 0, 0, 0.07);
    display: flex;
    align-items: center;
    padding: 1.56em;
    img{
      width: 3.125em;
      flex-shrink: 0;
      margin-right: .94em;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 131.25%;
    font-weight: 500;
  }
  input:checked~.appeal-type__inner{
    border-color: $primary;
    outline-color: $primary !important;
  }
}

.appeal-form{
  border-radius: .1875em;
  border: $border2;
  padding: 1.56em;
  @include grid100gap(1.56em);
  margin-top: 3em;
  textarea{
    height: 7.25em;
  }
}

.work-duration{
  margin-top: 2.56em;
}

.appeals{
  &__items{
    @include grid100gap(1.56em);
  }
}

.appeal-card{
  padding: 1.56em;
  border-radius: 0.1875em;
  border: $border2;
  display: flex;
  justify-content: space-between;
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: .375em;
  }
  &__badge{
    margin-left: .625em;
    border-radius: 0.3125em;
    background: #32A6C3;
    padding: 0 .5em;
    height: 1.8125em;
    display: flex;
    align-items: center;
    color: #FFF;
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &._yellow{
      background: $yellow;
      color: #342914;
    }
    &._green{
      background: #1B9665;
    }
  }
  &__desc{
    font-size: .9375em;
    line-height: 180%;
    color: $textGray;
    b{
      font-weight: 500;
    }
  }
  &__date{
    margin-left: 1em;
    flex-shrink: 0;
    font-size: .8125em;
    line-height: 223%;
    color: $textGray;
  }
}

.appeals-top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__info{
    @include grid100gap(.625em);
  }
  &__desc{
    font-size: .9375em;
    line-height: 180%;
    b{
      font-weight: 500;
    }
  }
  &__btn{
    padding: 0 1.25em;
    height: 2.9375em;
    margin-left: 1.56em;
    span{
      font-size: .75em;
    }
  }
}

.comments{
  @include grid100gap(.94em);
}

.comment-card{
  padding: 1.25em;
  &__top{
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 7em;
  }
  &__icon{
    width: 4.375em;
    height: 4.375em;
    flex-shrink: 0;
    @include center;
    background-color: #EDF5F8;
    border-radius: 50%;
    margin-right: 1em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 50%;
    }
  }
  &__initial{
    font-size: 1.3125em;
    line-height: 128%;
    color: $blue;
    font-weight: 800;
  }
  &__name{
    font-size: 1.125em;
    line-height: 150%;
    font-weight: 500;
  }
  &__wrap,
  &__type{
    display: flex;
    align-items: center;
  }
  &__type span,
  &__date{
    font-size: .8125em;
    line-height: 200%;
    color: $textGray;
  }
  &__type{
    &:before{
      content: '';
      flex-shrink: 0;
      width: 0.25em;
      height: 0.25em;
      margin: 0 .625em;
      background-color: #363A3A;
      border-radius: 50%;
    }
  }
  &__content{
    padding: .625em 0 .625em 5.38em;
    border-bottom: $border2;
  }
  &__images{
    display: flex;
    flex-wrap: wrap;
  }
  &__img{
    width: 6.375em;
    height: 5.25em;
    flex-shrink: 0;
    margin: .625em .625em 0 0;
    display: block;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: .125em;
    }
  }
  &._new{
    border-radius: 0.1875em;
    background-color: #fff;
    box-shadow: 0 .25em 1em 0 rgba(72, 134, 150, 0.16);
    .comment-card__content{
      border-bottom: none;
    }
  }
  .appeal-card__badge{
    position: absolute;
    top: 0;
    right: 0;
  }
}

.comment-question{
  border-radius: 0.1875em;
  border: 1px dashed #E7E9EA;
  background: #F8FBFC;
  @include center;
  color: $blue;
  padding: 1.19em;
  &__btn{
    margin-left: 1.44em;
    padding: 0 1.44em;
    height: 2.8125em;
    font-weight: 700;
    text-transform: uppercase;
    span{
      font-size: .75em;
    }
  }
}

.comments-feedback{
  .appeal-form{
    padding: 0;
    border: none;
  }
}


@media screen and (max-width: 992px){
  .new-appeal{
    margin-top: 1.56em;
    &__types{
      margin-top: .94em;
      grid-template-columns: repeat(2,calc(50% - .78em));
      gap: 1.56em;
    }
  }

  .appeal-form{
    gap: .94em;
    margin-top: 1.56em;
  }

  .work-duration{
    margin-top: 1.56em;
  }

  .appeals-top{
    align-items: flex-start;
  }

  .comment-card{
    &__top{
      padding-right: 6em;
    }
    &__icon{
      width: 2.5em;
      height: 2.5em;
    }
    &__initial{
      font-size: .75em;
    }
    &__name{
      font-size: .875em;
      line-height: 135%;
    }
    &__content{
      padding-left: 3.5em;
    }
    &__img{
      width: 4.125em;
      height: 3.375em;
    }
  }

  .comment-question{
    padding: .94em;
  }
}


@media screen and (max-width: 576px){
  .new-appeal{
    &__types{
      grid-template-columns: 100%;
      gap: .94em;
    }
  }

  .appeal-form{
    padding: .94em;
  }

  .appeals{
    &__items{
      gap: .94em;
    }
  }

  .appeal-card{
    padding: .94em;
    display: block;
    &__info{
      margin-bottom: .625em;
    }
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__badge{
      margin: 0 0 .375em;
      order: -1;
    }
    &__desc{
      font-size: .875em;
      line-height: 193%;
    }
    &__date{
      line-height: 123%;
      margin: 0;
    }
  }

  .appeals-top{
    flex-direction: column;
    &__btn{
      margin: 0 0 1.56em;
      order: -1;
    }
  }

  .comment-card{
    &__top{
      padding: 0;
    }
    &__icon{
      margin-right: .625em;
    }
    &__content{
      padding: .94em 0;
    }
    &__wrap{
      display: block;
    }
    &__type{
      margin-top: .12em;
      &:before{
        display: none;
      }
    }
    &__type span,
    &__date{
      line-height: 123%;
    }
    &._new{
      .comment-card__top{
        padding: 2.75em 0 0;
      }
    }
    .appeal-card__badge{
      right: unset;
      left: 0;
    }
  }

  .comment-question{
    flex-direction: column;
    text-align: center;
    &__btn{
      margin: .94em 0 0;
      width: 100%;
    }
  }
}
