.user-data{
  @include grid100gap(1.875em);
  @include lastM(0 0 3.125em);
  &__form{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
  }
}

.user-addresses{
  &._new-address{
    .user-addresses__add{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      ._plus{
        opacity: 0;
      }
    }
  }
  &__items{
    @include grid100gap(1.875em);
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__add{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $blue;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
      span{
        border-color: $primary;
      }
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .44em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
      border-bottom: 1px dashed $blue;
    }
    ._view2{
      display: none;
    }
  }
  &__new{
    margin-top: 1.25em;
    display: none;
    &-wrap{
      display: flex;
      align-items: center;
    }
    &-btn{
      flex-shrink: 0;
      width: 3.6875em;
      height: 3.6875em;
      padding: 0;
      margin-left: .625em;
      border-radius: 0.5625em;
      img{
        width: 1.5em;
      }
    }
  }
  &__buttons{
    margin-top: 2.5em;
  }
}

.address-card{
  border-radius: 0.1875em;
  border: $border2;
  &._type2{
    .address-card__bottom{
      @include grid100gap(.625em);
    }
  }
  &__title{
    height: 3.3125em;
    display: inline-flex;
    align-items: center;
    padding: 0 1.56em;
    background-color: $lightGray;
    font-weight: 500;
    color: $blue;
  }
  &__bottom{
    padding: 1.19em 1.56em 1.56em;
    display: flex;
  }
  &__field{
    flex-grow: 1;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    width: 3.6875em;
    height: 3.6875em;
    @include center;
    border-radius: 0.5625em;
    background-color: $lightGray;
    margin-left: .625em;
    svg,
    img{
      width: 1.5em;
      height: auto;
    }
    ._view2{
      display: none;
    }
    &._edit{
      &:hover{
        background-color: $yellow;
      }
    }
    &._delete{
      &:hover{
        background-color: $primary;
        path{
          stroke: #fff;
        }
      }
    }
  }
  &._active{
    ._edit{
      background-color: $yellow;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.form-buttons{
  display: grid;
  grid-template-columns: calc(100% - 13.75em - .875em) 13.75em;
  gap: .875em;
  max-width: 30.75em;
  .m-btn{
    width: 100%;
  }
}

.pass-form{
  max-width: 30.75em;
  @include grid100gap(1.56em);
}


@media screen and (max-width: 992px){
  .user-data{
    gap: 1.56em;
    @include lastM(0 0 1.56em);
  }

  .user-addresses{
    &__items{
      gap: .94em;
    }
    &__buttons,
    &__nav{
      margin-top: 1.56em;
    }
    &__new{
      margin-top: .94em;
    }
  }

  .address-card{
    &__bottom{
      padding: .94em;
    }
  }

  .pass-form{
    max-width: 100%;
  }
}


@media screen and (max-width: 576px){
  .user-data{
    gap: .94em;
    &__form{
      grid-template-columns: 100%;
      gap: .94em;
    }
  }

  .user-addresses{
    &__nav{
      margin-top: .94em;
    }
    &__new{
      &-btn{
        width: 2.25em;
        height: 2.25em;
        img{
          width: .875em;
        }
      }
    }
    &__buttons{
      margin-top: 1.875em;
    }
  }

  .form-buttons{
    grid-template-columns: 100%;
    gap: .875em;
  }

  .address-card{
    &__bottom{
      display: block;
    }
    &__nav{
      margin-top: .94em;
      justify-content: flex-end;
    }
    &__btn{
      width: 2.25em;
      height: 2.25em;
      border-radius: 0.34325em;
      img,svg{
        width: 1.125em;
      }
    }
  }

  .pass-form{
    gap: .9375em;
    .form-buttons{
      margin-top: .9375em;
    }
  }
}
