.text{
  p{
    font-size: 1.0625em;
    line-height: 165%;
    @include lastM(0 0 1em);
  }
}

.text._small,
.article{
  h3,h4{
    &:last-child{
      margin-bottom: 0;
    }
  }
  h3{
    font-size: 2.125em;
    line-height: 115%;
    font-weight: 800;
    font-family: $font2;
    margin-bottom: .59em;
  }
  h4{
    font-family: $font2;
    font-size: 1.25em;
    line-height: 145%;
    font-weight: 700;
    margin-bottom: .75em;
  }
  li,
  p{
    font-size: .9375em;
    line-height: 173%;
  }
  ul,
  p{
    @include lastM(0 0 1em);
  }
  b{
    font-weight: 700;
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
  }
  ul{
    @include grid100gap(1.25em);
    li{
      position: relative;
      padding-left: 2em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.53em;
        height: 1.93em;
        background-image: url(../img/logo3.svg);
        @include bgImgSize(100%);
      }
    }
  }
  blockquote{
    @include lastM(0 0 1.25em);
    border: .5625em solid $lightGray;
    padding: 1em 1em 1em 3.875em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 1em;
      left: 1em;
      background-image: url(../img/quote.svg);
      @include bgImgSize(100%);
      width: 2.25em;
      height: 2.25em;
    }
    p{
      font-size: 1em;
      line-height: 162%;
    }
  }
  a{
    text-decoration: underline;
    color: $primary;
  }
  &._list2{
    ul{
      gap: .625em;
      li{
        padding-left: 2em;
        &:before{
          width: 1.33em;
          height: 1.33em;
          top: .2em;
          background-image: url(../img/check6.svg);
        }
      }
    }
  }
  &._list3{
    ul{
      gap: .19em;
      padding-left: .6em;
      li{
        padding-left: .8em;
        position: relative;
        &:before{
          content: '•';
          position: absolute;
          top: 0;
          left: 0;
          background-image: unset;
          width: unset;
          height: unset;
        }
      }
    }
  }
}

@media screen and (max-width: 992px){
  .text._small,
  .article{
    h3{
      font-size: 1.25em;
      line-height: 120%;
      margin-bottom: 1em;
    }
    h4{
      font-size: 1.0625em;
      line-height: 130%;
    }
    li,
    p{
      font-size: .875em;
      line-height: 143%;
    }
    hr{
      height: .75em;
    }
    blockquote{
      padding: .6875em .6875em .6875em 2.875em;
      &:before{
        top: .6875em;
        left: .6875em;
        width: 1.5625em;
        height: 1.5625em;
      }
      p{
        font-size: .9375em;
        line-height: 173%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    p{
      font-size: .9375em;
      line-height: 173%;
    }
  }

  .article{
    blockquote{
      padding: 2.875em .6875em .6875em .6875em;
      p{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }
}
