.catalog-search{
  margin-top: 1.5625em;
  max-width: 57.4375em;
}

.category-nav{
  padding: 1.56em;
  border-radius: 0.1875em;
  border: $border;
  margin-bottom: 1.875em;
  &__link{
    display: flex;
    align-items: center;
    padding: .625em 1.25em;
    font-weight: 500;
    span{
      font-size: 1em;
      line-height: 150%;
    }
    &._active{
      color: $primary;
    }
  }
  &__bg,
  &__close{
    display: none;
  }
}

.category-toggle{
  display: none;
}

.search-empty{
  padding: 1.56em 0 7.5em;
  .text62{
    text-align: center;
  }
  &__inner{
    margin: 0 auto;
    width: 57.4375em;
    max-width: 100%;
  }
  .catalog-search{
    margin-top: 1.75em;
  }
  .info-message{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .category-nav{
    @include menu;
    border-radius: 0;
    border: none;
  }

  .category-toggle{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    border: $border3;
    padding: .94em 1.56em;
    margin-bottom: 1.56em;
    &__desc{
      display: flex;
      align-items: center;
      margin-left: 1em;
      color: #7E7E7E;
      max-width: 24.4375em;
      svg{
        width: .625em;
        height: auto;
        margin-left: .5em;
        flex-shrink: 0;
      }
    }
  }

  ._open-category{
    .category-nav{
      transform: translateX(0);
      &__bg{
        display: block;
      }
      &__close{
        display: flex;
      }
    }
  }

  .search-empty{
    padding: 1.56em 0 3.125em;
    .catalog-search{
      margin-top: 1.56em;
    }
    .info-message{
      margin-top: 1.56em;
    }
  }
}


@media screen and (max-width: 576px){
  .category-toggle{
    padding: .94em 1.25em;
    display: grid;
    grid-template-columns: 6.2em calc(100% - 6.2em);
    &__desc{
      &-text{
        width: calc(100% - 1.125em);
        .text14{
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }

  .search-empty{
    .catalog-search{
      margin-top: 1.875em;
    }
    .info-message{
      margin-top: 1.875em;
    }
  }
}
