.order-thank{
  padding: 1.56em 0 7.5em;
  &__inner{
    margin-top: 2.5em;
    border: $border2;
    border-radius: .1875em;
    max-width: 65.125em;
  }
  &__top{
    padding: 1.56em;
    max-width: 58em;
  }
  &__info{
    @include grid100gap(.44em);
  }
  &__count{
    font-size: .9375em;
    line-height: 140%;
    color: #676D6F;
  }
  &__items{
    margin-top: .94em;
    @include grid100gap(1.25em);
    .prop-item{
      &__title{
        font-size: 1.125em;
        font-weight: 500;
      }
    }
  }
  &__item{
    @include grid100gap(.34em);
  }
  &__sum{
    margin-top: .94em;
    &-title{
      color: #5D6263;
      font-size: .9375em;
      line-height: 140%;
    }
    &-num{
      font-size: 2.25em;
      line-height: 140%;
      font-weight: 500;
      color: $blue;
    }
  }
  &__bottom{
    padding: 1.56em;
    border-top: $border2;
    @include grid100gap(.94em);
  }
  &__detail{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    &-title{
      font-size: 1em;
      line-height: 140%;
      color: rgba(9, 9, 9, 0.50);
    }
    &-desc{
      font-size: .9375em;
      line-height: 140%;
    }
  }
}


@media screen and (max-width: 992px){
  .order-thank{
    padding-bottom: 3.125em;
    &__inner{
      margin-top: 1.56em;
    }
    &__count{
      font-size: .875em;
    }
    &__items{
      .prop-item{
        &__title,
        &__desc{
          font-size: .875em;
          line-height: 143%;
        }
      }
    }
    &__sum{
      &-title{
        font-size: .875em;
        line-height: 143%;
      }
      &-num{
        font-size: 1.75em;
      }
    }
    &__detail{
      &-title,
      &-desc{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .order-thank{
    &__top,
    &__bottom{
      padding: .94em;
    }
    &__items{
      margin-top: 1.56em;
      .prop-item{
        &:after{
          display: none;
        }
        &__desc{
          margin-left: 1.5em;
          white-space: nowrap;
        }
      }
    }
    &__sum{
      margin-top: 1.56em;
      &-num{
        font-size: 1.625em;
      }
    }
    &__detail{
      grid-template-columns: repeat(2,calc(50% - .78em));
      gap: 1.56em;
    }
  }
}
