.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    background: #fff;
    z-index: 19;
    padding: 1.56em 0;
    width: 100%;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0px -1px 55px 0px rgba(0, 0, 0, 0.08);
    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__content{
        @include grid100gap(.5em);
        max-width: 57.5em;
    }
    &__title{
        line-height: 132%;
        font-weight: 500;
    }
    &__desc{
        font-size: .9375em;
        line-height: 167%;
        color: #383838;
        a{
            text-decoration: underline;
        }
    }
    &__nav{
        display: flex;
        margin-left: 1.56em;
        flex-shrink: 0;
    }
    &__btn{
        max-width: 100%;
        padding: 0 2.06em;
        height: 2.5625em;
        text-transform: uppercase;
        border-width: 2px;
        font-weight: 700;
        span{
            font-size: .75em;
        }
    }
}

.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(#030E11, 0.95);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 2em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }
    &__dialog {
        background: #fff;
        width: 33.0625em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        display: inline-grid;
        grid-template-columns: 100%;
        gap: 1.25em;
        border-radius: 0.1875em;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 1.125em;
        @include center;
        position: absolute;
        left: 100%;
        bottom: 100%;
        margin: 0 0 .875em .875em;
        &:hover{
            path{
                opacity: 1;
            }
        }
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                opacity: 1;
            }
        }
    }
    &__top{
        @include grid100gap(1.25em);
    }
    &__text{
        letter-spacing: 0.02em;
        line-height: 206%;
        color: #534E54;
    }
}

.modal-form{
    @include grid100gap(1.56em);
    &__nav{
        display: flex;
        justify-content: center;
        .m-btn{
            width: 100%;
        }
    }
    &__checks{
        @include grid100gap(.75em);
    }
}

.modal-product{
    background: #F7F9FA;
    padding: .9375em;
    display: flex;
    align-items: center;
    &__img{
        width: 7.375em;
        height: 5.5625em;
        border-radius: 0.1875em;
        border: $border2;
        background-color: #fff;
        margin-right: .94em;
        flex-shrink: 0;
        @include imgCover(100%);
        padding: 0;
    }
    &__title{
        font-size: 1em;
        line-height: 131%;
        font-weight: 500;
        @include truncate(2);
    }
    .product-prices{
        margin-top: .25em;
        &__item{
            display: flex;
            align-items: center;
        }
        .m-badge{
            margin-left: .44em;
            font-weight: 700;
            padding: 0 .56em;
            height: 1.625em;
        }
    }
}

.thank-modal{
    .modal__dialog{
        width: 42.8125em;
        gap: .94em;
        text-align: center;
    }
    .thank-form{
        &__img{
            width: 3.9375em;
            margin: 0 auto;
        }
        &__text{
            margin: 0 auto;
        }
    }
}

.product-alert,
.info-alert{
    opacity: 0;
    pointer-events: none;
    position: fixed;
    right: 3.125em;
    z-index: 20;
    border-radius: 0.1875rem;
    background: #fff;
    box-shadow: 0 .8125em 1.6875em 0 rgba(0, 0, 0, 0.10);
    padding: .94em;
    width: 24.8125em;
    display: flex;
    align-items: center;
    transition: all .3s;
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &__close{
        width: .625em;
        @include center;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                opacity: 1;
            }
        }
    }
}

.product-alert{
    top: 3.125em;
    &__img{
        margin-right: .94em;
        width: 5.5em;
        height: 5.5em;
        flex-shrink: 0;
        border-radius: 0.1875em;
        border: $border2;
        img{
            border-radius: inherit;
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
        }
    }
    &__content{
        @include grid100gap(.19em);
    }
    &__message{
        text-transform: uppercase;
        font-size: 0.75em;
        line-height: 167%;
        font-weight: 500;
        color: $blue;
        opacity: .5;
    }
    &__title{
        font-size: 1.0625em;
        line-height: 135%;
        font-weight: 500;
        @include truncate(2);
    }
    .product-prices{
        &__old{
            font-size: 0.8125em;
        }
        &__current{
            font-size: 1.0625em;
        }
    }
    &__close{
        position: absolute;
        top: .625em;
        right: .625em;
    }
}

.info-alert{
    bottom: 3.125em;
    &__icon{
        width: 1.5em;
        flex-shrink: 0;
        margin-right: .44em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__title{
        margin-right: .75em;
    }
    &__close{
        margin-left: auto;
    }
}

.map-modal{
    .modal__dialog{
        width: 80em;
    }
    .map{
        height: 37.5em;
    }
}


@media screen and (max-width: 992px){
    .cookie-modal{
        padding: 1.25em 0;
        &__desc{
            font-size: .875em;
        }
    }

    .modal{
        padding: 5em 0;
        &__dialog{
            gap: .94em;
            padding: 1.25em;
        }
        &__close{
            position: absolute;
            top: unset;
            bottom: 100%;
            width: .875em;
            margin: 0 -.625em .875em 0;
        }
        &__top{
            gap: .75em;
        }
        &__nav{
            .m-btn{
                width: 100%;
            }
        }
        .m-check{
            label{
                span{
                    font-size: .8125em;
                    line-height: 130%;
                }
            }
        }
    }

    .modal-form{
        gap: .94em;
        &__nav{
            .m-btn{
                width: 100%;
            }
        }
    }

    .product-alert{
        top: 10.3125em;
        right: 1.56em;
    }

    .info-alert{
        right: 1.56em;
        bottom: 1.56em;
    }
}


@media screen and (max-width: 576px){
    .cookie-modal{
        &__inner{
            display: block;
        }
        &__nav{
            margin: 1.56em 0 0;
        }
        &__btn{
            width: 100%;
        }
    }

    .modal{
        &__dialog{
            padding-left: .9375em;
            padding-right: .9375em;
        }
        &__close{
            margin: 0;
            bottom: unset;
            left: unset;
            top: .94em;
            right: .94em;
            path{
                stroke: $black;
            }
        }
        &__top{
            padding-right: 2em;
        }
    }

    .modal-product{
        padding: .625em;
        &__img{
            width: 5.5625em;
            height: 4.1875em;
            margin-right: .625em;
        }
        &__title{
            font-size: .875em;
            line-height: 114%;
        }
        .product-prices{
            .m-badge{
                padding: 0 .5em;
                height: 1.5em;
            }
        }
    }

    .thank-modal{
        .thank-form{
            &__img{
                width: 3.75em;
            }
        }
    }

    .info-alert,
    .product-alert{
        top: unset;
        bottom: 1.875em;
        right: .9375em;
        width: calc(100% - 1.875em);
        &__img{
            display: none;
        }
    }

    .info-alert{
        padding-right: .625em;
        &__title{
            margin-right: .125em;
            .text15{
                font-size: .875em;
            }
        }
    }
}
