.news-page{
  padding: 1.56em 0 5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 20.4375em - 1.9375em) 20.4375em;
    gap: 1.9375em;
    margin-top: 1.56em;
  }
}

.news-filter{
  margin-top: 2.5em;
  &__links{
    display: flex;
  }
  &__link{
    height: 2.8125em;
    padding: 0 1.875em;
    font-weight: 500;
    border-radius: 0.1875em;
    border: 1px solid #F2F4F5;
    @include lastM(0 .625em 0 0);
    span{
      font-size: .875em;
    }
    &._active{
      pointer-events: none;
    }
    &._active,
    &:hover{
      background-color: $blue;
      border-color: $blue;
      color: #fff;
    }
  }
}

.news-nav{
  position: sticky;
  top: 1em;
  &__show{
    display: none;
  }
  &__link{
    padding: 1em 1.875em;
    display: flex;
    align-items: center;
    min-height: 3.5em;
    background-color: $lightGray;
    font-weight: 500;
    &:first-child{
      border-top-left-radius: .1875em;
      border-top-right-radius: .1875em;
    }
    &:last-child{
      border-bottom-left-radius: .1875em;
      border-bottom-right-radius: .1875em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover,
    &._active{
      background-color: $primary;
      color: #fff;
    }
  }
}

.pagination{
  margin-top: 3.75em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.625em;
    min-width: 3.625em;
    padding: 0 1.25em;
    border-radius: 0.25em;
    border: $border;
    @include center;
    margin: 0 .3125em;
    font-weight: 500;
    svg{
      width: 1.0625em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $black;
      border-color: $black;
      path{
        fill: #fff;
      }
    }
    &._btn{
      padding: 0 1.5em;
    }
  }
}

.news-item{
  padding: 1.56em 0;
  &__date{
    margin-top: .75em;
  }
  &__desc{
    margin-top: 2.19em;
    padding: 1.56em;
    background-color: $lightGray;
    .text17{
      line-height: 171%;
    }
  }
  &__thumb{
    margin-top: 2.19em;
    img{
      display: block;
      width: 100%;
      max-width: 72.625em;
      margin: 0 auto;
    }
    &-title{
      text-align: center;
      margin-top: 1em;
      color: #494949;
      font-size: .9375em;
      line-height: 173%;
    }
  }
  .article{
    margin-top: 2.19em;
  }
}

.article-group{
  display: flex;
  @include lastM(0 0 .9375em);
  &__img{
    flex-shrink: 0;
    width: 31.5625em;
    margin-right: 2.19em;
    img{
      border: $border2;
      width: 100%;
      display: block;
    }
  }
  &__content{
    flex-grow: 1;
  }
  &._reverse{
    .article-group__img{
      width: 33.875em;
      margin: 0 0 0 2.19em;
      order: 2;
    }
  }
}


@media screen and (max-width: 992px){
  .news-page{
    padding: 1.56em 0 3.125em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
      margin-top: 0;
    }
    &__sidebar{
      order: -1;
    }
  }

  .news-filter{
    margin-top: 1.56em;
  }

  .news-nav{
    top: unset;
    width: 14em;
    margin-left: auto;
    margin-top: -2.875em;
    position: relative;
    &__show{
      cursor: pointer;
      display: flex;
      align-items: center;
      height: 2.8125em;
      border-radius: .1875em;
      border: $border2;
      padding: 0 1.56em;
      justify-content: space-between;
      svg{
        width: .75em;
        height: auto;
        margin-left: .25em;
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      border-radius: .1875em;
      overflow: hidden;
      border: none;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__link{
      border-radius: 0 !important;
      min-height: 2.8125em;
      padding: .75em 1.56em;
      span{
        font-size: .875em;
      }
      &._active{
        display: none;
      }
    }
    &._show{
      .news-nav__show{
        background-color: $primary;
        border-color: $primary;
        color: #fff;
        path{
          stroke: #fff;
        }
        svg{
          transform: rotate(-180deg);
        }
      }
      .news-nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .pagination{
    margin-top: 1.875em;
  }

  .news-item{
    &__desc{
      margin-top: 1.5em;
      padding: 1.25em;
      .text17{
        font-size: .875em;
        line-height: 143%;
      }
    }
    &__thumb{
      margin-top: 1.56em;
      &-title{
        font-size: .875em;
        line-height: 143%;
      }
    }
    .article{
      margin-top: 1.56em;
    }
  }

  .article-group{
    flex-direction: column;
    &__img{
      margin: 0 auto 2.19em;
      max-width: 100%;
    }
    &__content{
      width: 100%;
    }
    &._reverse{
      .article-group__img{
        margin: 2.19em auto 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .news-page{
    &__inner{
      gap: 1.56em;
      margin-top: 1.56em;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .news-filter{
    &__link{
      padding: 0 1em;
      height: 2.125em;
      flex-grow: 1;
    }
  }

  .news-nav{
    width: 100%;
    margin-top: 0;
  }

  .pagination{
    margin-top: 1.56em;
    ._hide-tablet{
      display: none;
    }
    ._btn{
      width: 2.8125em;
      padding: 0;
    }
    &__item{
      height: 2.8125em;
      min-width: 2.8125em;
      padding: 0 .875em;
      span{
        font-size: .875em;
      }
    }
  }
}
