.faq-top{
  @include grid100gap(1.25em);
  &__desc{
    font-size: 1em;
    line-height: 140%;
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
}

.tabs-select{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    border: 1px solid #DADEDF;
    background-color: #fff;
    padding: 0 1.25em;
    height: 3.125em;
    &:before{
      content: '';
      order: 3;
      border-left: $border3;
      flex-shrink: 0;
      width: 1.875em;
      height: 1.25em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(.5em);
      background-position: right center;
      margin-left: 1em;
    }
  }
  &__current{
    font-size: .875em;
    font-weight: 500;
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    top: 100%;
    margin-top: -1px;
    border-radius: 0 0 .1875em .1875em;
    width: calc(100% + 2px);
    left: -1px;
    border: $border3;
    background: #FFF;
    box-shadow: none;
    max-height: 16em;
    overflow-y: auto;
    z-index: 5;
    @include scroll;
    position: absolute;
  }
  .tab2{
    cursor: pointer;
    transition: all .3s;
    height: 3em;
    padding: .5em 1.25em;
    display: flex;
    align-items: center;
    color: #2B2B2B;
    border-bottom: $border3;
    font-weight: 500;
    &:last-child{
      border-bottom: none;
    }
    span{
      font-size: .875em;
      line-height: 157%;
    }
    &:hover,
    &._active{
      background-color: $primary;
      border-color: $primary;
      color: #fff;
    }
  }
  &._show{
    .tabs-select__show{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    .tabs-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._mob{
    @media screen and (min-width: 577px){
      .tabs-select__show{
        display: none;
      }
      .tabs-select__dropdown{
        border: none;
        max-height: unset;
        opacity: 1;
        pointer-events: unset;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        margin-top: -.625em;
        position: static;
      }
      .tab2{
        border-radius: 0.1875em;
        border: 1px solid #F2F4F5 !important;
        padding: 0 1.25em;
        height: 2.8125em;
        margin: .625em .625em 0 0;
        &._active,
        &:hover{
          background: $blue;
          color: #fff;
          border-color: $blue !important;
        }
      }
    }
  }
}

.qa-card{
  &._active{
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    padding: .94em 0;
    border-bottom: $border2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__hidden{
    display: none;
    padding: .94em;
    border-bottom: $border2;
  }
  .text{
    color: $textGray;
  }
}

.leasing-info{
  margin-top: 2.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 11.875em;
    margin-bottom: .69em;
  }
  &__items{
    width: 39.5em;
    max-width: 100%;
    margin-top: 1.69em;
    display: grid;
    grid-template-columns: 50% 50%;
    box-shadow: 0 0 1.1875em 0 rgba(0, 0, 0, 0.07);
    background-color: #fff;
    border-radius: .1875em;
  }
}

.leasing-item{
  padding: .94em;
  text-align: center;
  font-weight: 500;
  &:not(:last-child){
    border-right: $border2;
  }
  &__title{
    font-size: 1.125em;
    line-height: 150%;
  }
  &__desc{
    font-size: 1em;
    line-height: 181%;
    span{
      color: #FF780F;
    }
  }
}

.leasing-form{
  margin-top: 1.875em;
  .contacts-form{
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
  }
  &__title{
    grid-column: 1/-1;
    text-align: center;
  }
  .contacts-form__btn{
    width: 22.75em;
    height: 3.8125em;
    flex-shrink: 0;
  }
}


@media screen and (max-width: 992px){
  .faq-top{
    gap: 1.56em;
  }

  .leasing-info{
    margin-top: 1.56em;
    &__items{
      width: 37.5625em;
    }
  }

  .leasing-item{
    &__title{
      font-size: .875em;
      line-height: 136%;
    }
    &__desc{
      font-size: .9375em;
      line-height: 193%;
    }
  }

  .leasing-form{
    margin-top: 1.56em;
    .contacts-form__btn{
      margin-right: .94em;
    }
    .m-check{
      label{
        padding-left: 1.875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .leasing-info{
    &__img{
      width: 7.5em;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .leasing-item{
    &:not(:last-child){
      border-right: none;
      border-bottom: $border2;
    }
  }

  .leasing-form {
    .contacts-form {
      grid-template-columns: 100%;
      gap: .94em;
    }
    .contacts-form__btn{
      width: 100%;
      margin: 0 0 .94em;
    }
  }
}
