@import "../vars.scss";
.breadcrumbs {
    padding: .94em 0;
    &__item {
        color: #2B2F30;
        position: relative;
        display: inline;
        &:last-child {
            color: rgba(#2B2F30,.6);
        }
        &:not(:first-child) {
            margin-left: 1.75em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                color: #D1D8DA;
                top: .125em;
                right: 100%;
                margin-right: .75em;
                font-size: .875em;
                line-height: 157%;
            }
        }
        span{
            font-size: .875em;
            line-height: 157%;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        .container{
            @include truncate(1);
        }
        ._dots-mob{
            &:after{
                content: '...';
                font-size: .875em;
                line-height: 157%;
            }
            span{
                display: none;
            }
        }
        ._hide-mob{
            display: none;
        }
    }
}
