.lk{
  padding: 1.56em 0 7.5em;
  &__inner{
    display: grid;
    grid-template-columns: 20.5em calc(100% - 20.5em - 1.875em);
    gap: 1.875em;
  }
}

.lk-menu{
  position: sticky;
  top: 1em;
  padding: 1.56em;
  border-radius: 0.1875em;
  background: #FFF;
  box-shadow: 0 .25em 1.6875em 0 rgba(0, 0, 0, 0.07);
  &__title{
    ._view2{
      display: none;
    }
  }
  &__items{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
  &__bg,
  &__close{
    display: none;
  }
}

.menu-group{
  &__show{
    display: flex;
    align-items: center;
    padding: .625em 0;
  }
  &__icon{
    width: 1.6875em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 0.9375em;
    line-height: 173%;
    font-weight: 700;
  }
  &__link{
    display: flex;
    align-items: center;
    padding: .625em 2.31em;
    color: #3B4041;
    border-radius: 0.25em;
    &:hover{
      background: #F7F9FC;
      color: $blue;
    }
    span{
      font-size: 1em;
      line-height: 168.75%;
    }
    &._active{
      background: #F7F9FC;
      color: $blue;
      font-weight: 500;
      &:before{
        content: '';
        background-image: url(../img/arrow-right.svg);
        @include bgImgSize(100%);
        flex-shrink: 0;
        margin-right: .5em;
        width: .375em;
        height: .625em;
      }
    }
  }
}

.lk-menu-tablet{
  display: none;
}


@media screen and (max-width: 992px){
  .lk{
    padding-bottom: 3.125em;
    &__inner{
      grid-template-columns: 4.25em calc(100% - 4.25em - 1.56em);
      gap: 1.56em;
    }
  }

  .lk-menu-tablet{
    min-height: 100%;
    padding: 1.25em 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 0.1875em;
    background: #FFF;
    box-shadow: 0 .25em 1.6875em 0 rgba(0, 0, 0, 0.07);
    .menu-group__icon{
      margin: 0;
    }
    &__item{
      padding: .625em 0;
      @include lastM(0 0 .625em);
    }
  }

  .lk-menu{
    position: fixed;
    top: 0;
    left: 0;
    height: $vh;
    transform: translateX(-120%);
    transition: all .4s;
    z-index: 30;
    width: 13.125em;
    border-radius: 0;
    box-shadow: none;
    &__bg{
      background-color: rgba($black,.95);
      opacity: .9;
      width: 100%;
      height: $vh;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 20;
    }
    &__close{
      position: fixed;
      top: .25em;
      right: .25em;
      z-index: 21;
    }
    @media screen and (min-width: 577px){
      padding: 1.25em 0;
      &__title{
        display: none;
      }
      &__items{
        margin-top: 0;
      }
      .menu-group{
        &._open{
          .menu-group__show{
            background-color: $lightGray;
            &:after{
              transform: rotate(-180deg);
            }
          }
          .menu-group__hidden{
            opacity: 1;
            pointer-events: unset;
          }
        }
        &__show{
          padding-left: 1.25em;
          padding-right: 1.25em;
          transition: all .3s;
          &:after{
            content: '';
            margin-left: auto;
            background-image: url(../img/arrow-right2.svg);
            @include bgImgSize(100%);
            width: 0.625em;
            height: 0.5625em;
            flex-shrink: 0;
            transition: all .3s;
          }
        }
        &__hidden{
          position: absolute;
          top: 0;
          left: 100%;
          background-color: $lightGray;
          border-radius: 0;
          height: 100%;
          overflow-y: auto;
          min-width: 13.6875em;
          padding: 1.25em 0;
          transition: all .3s;
          opacity: 0;
          pointer-events: none;
        }
        &__link{
          padding: .625em 1.56em;
          height: 2.9375em;
          white-space: nowrap;
          span{
            font-size: .9375em;
            line-height: 120%;
          }
        }
      }
    }
  }

  ._open-profile-menu{
    .lk-menu{
      transform: translateX(0);
      &__bg{
        display: block;
      }
      &__close{
        display: flex;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .lk{
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .lk-menu{
    width: 20.625em;
    padding: 2.5em 1.56em;
    overflow-y: auto;
    &__title{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__items{
      margin-top: 1.56em;
      gap: 1.56em;
    }
  }

  .lk-menu-tablet{
    display: none;
  }

  .menu-group{
    &__show{
      padding: .3125em 0;
    }
    &__title{
      font-size: .875em;
    }
    &__link{
      padding: .625em 2em;
      &:before{
        margin-left: -.875em;
      }
    }
  }
}
