.return-change{
  &__top{
    display: flex;
    align-items: center;
  }
  &__hint{
    margin-left: 1.56em;
    display: flex;
    align-items: center;
    color: $blue;
    a{
      text-decoration: underline;
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  .info-message,
  .track-hint{
    margin-top: 2.5em;
  }
}

.lk-search{
  width: 35.3125em;
  flex-shrink: 0;
  .m-search__result{
    padding: .25em .9375em .25em 0;
  }
  &__items{
    max-height: 18.5em;
    overflow-y: auto;
    @include scroll;
  }
}

.order-row{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1em .94em;
  &__title{
    font-size: .9375em;
    line-height: 140%;
    font-weight: 500;
  }
  &__desc{
    font-size: .875em;
    line-height: 140%;
    color: #676D6F;
    margin-top: .1em;
  }
  &__btn{
    margin-left: .94em;
    flex-shrink: 0;
    height: 2.5em;
    padding: 0 1.44em;
    font-weight: 500;
    span{
      font-size: .875em;
    }
  }
}

.return-guide{
  margin-top: 2.5em;
  &__items{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.44em));
    gap: 1.94em 6.88em;
  }
  &__hint{
    margin-top: 2.19em;
    color: $textGray;
  }
}

.return-result{
  margin-top: 1.875em;
  &__items{
    margin-top: 1em;
    border-radius: 0.1875em;
    border: $border2;
  }
}

.return-duration{
  margin-top: 2.5em;
}


@media screen and (max-width: 992px){
  .return-change{
    &__top{
      display: block;
    }
    &__hint{
      margin: .94em 0 0;
      padding-left: .625em;
    }
    .info-message,
    .track-hint{
      margin-top: 1.56em;
    }
  }

  .lk-search{
    width: 100%;
  }

  .return-guide{
    margin-top: 1.56em;
    &__items{
      margin-top: .94em;
      grid-template-columns: repeat(2,calc(50% - .78em));
      gap: 1.56em;
    }
    .advantage-item{
      &__icon{
        width: 2.25em;
      }
      &__title{
        font-size: .875em;
      }
      &__desc{
        font-size: .875em;
        line-height: 143%;
      }
    }
    &__hint{
      margin-top: .94em;
    }
  }

  .return-result{
    margin-top: 1.56em;
    &__items{
      margin-top: .94em;
    }
  }

  .return-duration{
    margin-top: 1.56em;
  }
}


@media screen and (max-width: 576px){
  .lk-search{
    .m-search__result{
      padding-right: .25em;
    }
    &__items{
      max-height: 23.3125em;
    }
  }

  .order-row{
    padding: .94em;
    &__title{
      font-size: .875em;
      line-height: 150%;
    }
    &__btn{
      padding: 0 .94em;
    }
  }

  .return-guide{
    &__items{
      grid-template-columns: 100%;
      gap: .94em;
    }
  }
}
