.price-list{
  &__nav{
    margin-top: 2.5em;
    display: flex;
    .m-btn{
      width: 16.25em;
    }
  }
}

.mailing{
  margin-top: 1.25em;
  &__items{
    display: flex;
    flex-wrap: wrap;
    .m-radio{
      margin: .625em 1.75em 0 0;
    }
  }
}


@media screen and (max-width: 992px){
  .price-list{
    &__nav{
      margin-top: 1.56em;
    }
  }

  .mailing{
    margin-top: 1.56em;
    &__items{
      .m-radio{
        margin: .94em .94em 0 0;
        label{
          padding-left: 1.625em;
          span{
            font-size: .875em;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .price-list {
    &__nav {
      .m-btn{
        width: 100%;
      }
    }
  }
}
