.collection{
  overflow: hidden;
  &+.collection{
    padding-top: 0;
  }
  &._last{
    padding-bottom: 7.5em;
  }
  &.pt60{
    @media screen and (min-width: 993px){
      padding-top: 3.75em;
    }
  }
}

.collection-slider,
.news-slider{
  margin-top: 2.5em;
  .swiper{
    &-container{
      overflow: hidden;
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 25%;
      padding-right: 1.875em;
    }
  }
}

.product-card{
  border-radius: 0.1875em;
  border: $border;
  background: #FFF;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(83.8%);
  }
  &__badges{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    top: 1.25em;
    left: 1.25em;
    z-index: 3;
    .m-badge{
      margin-bottom: .44em;
    }
  }
  .m-rating{
    position: absolute;
    bottom: 0;
    left: 1.25em;
  }
  &__content{
    padding: 1em 1.19em .69em;
  }
  &__title{
    font-weight: 500;
    line-height: 150%;
    @include truncate(2);
  }
  .product-prices{
    margin-top: .44em;
    .m-badge{
      display: none;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    padding-left: 2.5em;
    min-height: 3.1875em;
    border-top: $border;
    margin-top: auto;
  }
  .favourite-btn{
    margin-right: 2.81em;
  }
  .cart-btn{
    margin-left: auto;
    margin-top: -1px;
    margin-right: -1px;
    border-radius: 0 0 0.1875em 0;
  }
}

.m-badge{
  padding: 0 .56em;
  height: 1.625em;
  min-width: 2.6875em;
  @include center;
  border-radius: 0.1875em;
  background: #FF6565;
  text-transform: uppercase;
  color: #fff;
  font-weight: 800;
  span{
    font-size: 0.6875em;
    display: block;
    transform: translateY(12%);
  }
  &._green{
    background: #1B9665;
    span{
      font-size: .8125em;
      transform: unset;
    }
  }
}

.m-rating{
  display: flex;
  align-items: center;
  padding: 0 .56em;
  height: 1.875em;
  border-radius: 56.25em;
  background-color: $blue;
  color: #fff;
  svg{
    width: .75em;
    margin-right: .31em;
  }
}

.product-prices{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 .44em 0 0);
  }
  &__current{
    font-size: 1.375em;
    line-height: 140%;
    font-weight: 500;
    color: $blue;
  }
  &__old{
    font-size: 1.0625em;
    line-height: 140%;
    color: #8B8E8F;
    text-decoration: line-through;
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  width: 1.4375em;
  &:hover{
    path{
      opacity: 1;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $primary;
      fill: $primary;
    }
  }
}

.compare-btn{
  width: 1.5em;
  &:hover{
    g{
      opacity: 1;
    }
  }
  &._added{
    g{
      opacity: 1;
    }
    line{
      stroke: $primary;
    }
  }
}

.cart-btn{
  min-width: 9.9375em;
  height: 3.1875em;
  text-transform: uppercase;
  font-weight: 700;
  padding: 0 1.5em;
  img{
    width: 1.375em;
    margin-left: .81em;
    &._view2{
      width: 1.125em;
    }
  }
  ._view2{
    display: none;
  }
  &._added{
    background: #A53028;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .collection{
    &._last{
      padding-bottom: 3.125em;
    }
  }

  .collection-slider,
  .news-slider{
    margin-top: 1.875em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
    }
  }

  .m-rating{
    padding: 0 .5em;
    height: 1.625em;
  }

  .product-card{
    border: none;
    &__img{
      border: $border2;
      border-radius: .1875em;
    }
    &__badges{
      top: .625em;
      left: .625em;
      ._sale-badge{
        display: none;
      }
    }
    .m-rating{
      background-color: transparent;
      color: $black;
      padding: 0;
      height: unset;
      position: static;
      margin-top: .31em;
      path{
        fill: $yellow;
      }
    }
    &__content{
      padding: 0;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
    }
    &__title{
      font-size: .875em;
      line-height: 135%;
      -webkit-line-clamp: 3;
    }
    .product-prices{
      margin-top: auto;
      padding-top: .13em;
      display: block;
      .m-badge{
        display: flex;
        height: 1.25em;
        padding: 0 .12em;
        margin-left: .625em;
        span{
          font-size: .625em;
        }
      }
    }
    &__nav{
      min-height: unset;
      padding-top: .5em;
      border: none;
      padding-left: .31em;
      padding-right: .625em;
    }
    .cart-btn{
      min-width: unset;
      width: 2.125em;
      height: 2.125em;
      padding: 0;
      border-radius: 0.1875em;
      span{
        display: none !important;
      }
      img{
        margin: 0;
        width: 1.125em;
      }
    }
    .favourite-btn{
      margin-right: 1.56em;
    }
  }

  .m-badge{
    padding: 0 .31em;
    height: 1em;
    min-width: 1.8125em;
    span{
      font-size: .5625em;
    }
  }

  .product-prices{
    &__item{
      display: flex;
      align-items: center;
    }
    &__old{
      font-size: 0.8125em;
      line-height: 114%;
    }
    &__current{
      font-size: 1em;
      line-height: 140%;
    }
  }

  .swiper-nav._type1{
    .swiper-button{
      width: 2.3125em;
      height: 2.3125em;
      margin: 0 .6875em;
    }
  }

  .swiper-nav._container{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
    .swiper-button{
      transform: unset;
      position: static;
    }
  }
}


@media screen and (max-width: 576px) {
  .collection-slider {
    .swiper {
      &-container {
        width: calc(100% + .9375em);
      }

      &-slide {
        width: 50%;
        padding-right: .9375em;
      }
    }
  }
}
