.organizations{
  @include grid100gap(1.875em);
}

.organization-card{
  border-radius: .1875em;
  border: $border2;
  &__top{
    padding: 1.56em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .address-card__nav{
    margin: 0;
  }
  .address-card__btn{
    width: 3.0625em;
    height: 3.0625em;
  }
  &__bottom{
    padding: 1.56em;
    border-top: $border2;
    @include grid100gap(1.19em);
  }
}

.organization-edit{
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.56em);
    &:first-child{
      margin-top: 0;
    }
  }
}

.custom-file-container{
  height: 100%;
  width: 100%;
  min-height: 100%;
  position: static;
  .label-container{
    display: none;
  }
  .input-container{
    margin: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    z-index: 3;
    input{
      height: 100% !important;
    }
  }
  .image-preview{
    background-color: transparent;
    background-image: unset !important;
    padding: 0;
    height: auto;
    margin: 0;
    transform: translateY(-.75em);
    display: flex;
    flex-wrap: wrap;
    &:after{
      content: '';
      flex-shrink: 0;
      background-image: url(../img/plus.svg);
      @include bgImgSize(1.5em);
      align-self: center;
      margin-top: .75em;
      opacity: 0;
      width: 3.0625em;
      height: 3.0625em;
      border-radius: 0.1875em;
      border: 1px dashed #E7E9EA;
      pointer-events: none;
    }
  }
  .input-visible{
    opacity: 0;
    pointer-events: none;
  }
  .image-preview-item{
    width: 3.0625em;
    height: 3.0625em;
    border-radius: 0.1875em;
    margin: .75em .75em 0 0;
    box-shadow: none;
    float: unset;
    &-clear{
      display: none;
    }
  }
}

.file-input{
  input{
    //opacity: 1;
  }
  &._uploaded{
    .file-input__title{
      opacity: 0;
    }
    .file-input__upload{
      opacity: 1;
    }
    .image-preview:after{
      opacity: 1;
    }
  }
  &__wrap{
    position: relative;
    min-height: 3.0625em;
    padding-left: 3.81em;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    pointer-events: none;
  }
  &__btn{
    width: 3.0625em;
    height: 3.0625em;
    background-color: $blue;
    @include center;
    border-radius: 0.5625em;
    margin-right: .75em;
    img{
      width: .8125em;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
    font-weight: 500;
    color: $blue;
    border-bottom: 1px dashed $blue;
  }
}

.organization-add{
  @include grid100gap(1.56em);
  &__nav{
    display: flex;
    .m-btn{
      padding: 0 3.75em;
      margin-right: .875em;
    }
    .m-btn-red{
      padding: 0 2.41em;
    }
  }
  &__field{
    position: relative;
    &._show{
      .organization-add__variant{
        opacity: 1;
        pointer-events: unset;
      }
    }
    .fg{
      z-index: 3;
    }
  }
  &__variant{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 4.94em 1.25em 1.56em;
    z-index: 2;
    border-radius: 0.1875em;
    background-color: #fff;
    box-shadow: 0 .25em .4375em 0 rgba(0, 0, 0, 0.10);
    margin-top: 1.69em;
    input{
      position: relative;
      z-index: 2;
    }
  }
  .organization-card__bottom{
    border-radius: .1875em;
    border: $border2;
  }
}


@media screen and (max-width: 992px){
  .organization-card{
    &__top,
    &__bottom{
      padding: .94em;
    }
    &__bottom{
      gap: .94em;
    }
  }

  .organization-edit{
    &__items{
      margin-top: 1.56em;
    }
  }
}


@media screen and (max-width: 576px){
  .organization-card{
    .address-card__btn{
      width: 2.25em;
      height: 2.25em;
      margin-left: .94em;
    }
  }

  .organization-edit{
    &__items{
      margin-top: .94em;
      gap: .94em;
    }
  }

  .organization-add{
    gap: .94em;
    &__nav{
      margin-top: .625em;
      @include grid100gap(.625em);
      .m-btn{
        margin: 0;
        padding: 0;
        width: 100%;
      }
    }
  }
}
