.main-slider{
  position: relative;
  .swiper-pagination{
    z-index: 3;
    left: 5em;
    bottom: 3.75em;
    width: auto;
    max-width: 39em;
    @media screen and (min-width: 1561px){
      left: calc((100vw - 1560px) / 2 + 5em);
    }
  }
}

.main-slide{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  &:nth-child(even){
    background-image: url(../img/main-bg3.webp);
    @media screen and (min-width: 993px){
      .main-slide__img{
        right: -6em;
      }
    }
  }
  &__inner{
    position: relative;
  }
  &__img{
    pointer-events: none;
    right: 0;
    position: absolute;
    top: 0;
    //width: 48em;
    max-width: 64em;
    img{
      display: block;
      max-width: 100%;
      max-height: 48em;
    }
  }
  &__content{
    min-height: 40em;
    padding: 9em 0;
    max-width: 39em;
  }
  &__desc{
    margin-top: 1.25em;
    max-width: 27.9375em;
    color: #3B484A;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.swiper-nav._type1{
  .swiper{
    &-button{
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      box-shadow: 0 .9375em 1em rgba(68, 66, 60, 0.05);
      background-color: #fff;
      border-radius: 50%;
      width: 4.4375em;
      height: 4.4375em;
      @include center;
      &.swiper-button-disabled{
        background: #C7C3C3 !important;
      }
      &:hover{
        background-color: $blue;
        path{
          fill: #fff;
        }
      }
      svg{
        width: .5em;
      }
      &-prev{
        left: .5em;
      }
      &-next{
        right: .5em;
      }
      @media screen and (min-width: 1561px){
        &-prev{
          left: calc((100vw - 1560px) / 2 - 2em);
        }
        &-next{
          right: calc((100vw - 1560px) / 2 - 2em);
        }
      }
    }
  }
}

.swiper-nav._container {
  .swiper-button{
    &-prev{
      left: -4.75em;
    }
    &-next{
      right: -4.75em;
    }
    @media screen and (min-width: 1630px){
      &-prev{
        left: -7.5em;
      }
      &-next{
        right: -7.5em;
      }
    }
  }
}

.swiper-pagination{
  display: flex;
  align-items: center;
  &-bullet{
    border: 1px solid #A5AEB1;
    opacity: 1;
    background-color: #A5AEB1;
    width: 1.8125em;
    height: 0.25em;
    border-radius: 0;
    margin: 0 .94em 0 0 !important;
    &:last-child{
      margin-right: 0 !important;
    }
    &-active{
      pointer-events: none;
      background-color: transparent;
    }
    &:hover{
      background-color: $primary;
      border-color: $primary;
    }
  }
}

.swiper-scrollbar{
  margin-top: 1.875em;
  height: 0.3125em;
  background-color: $lightGray;
  &-drag{
    background-color: $blue;
  }
}

.slider{
  position: relative;
}

.m-section{
  padding: 5em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    height: 2.5em;
    padding: 0 1.25em;
    font-weight: 500;
    color: #0B3B47;
    border-color: #0B3B47;
    span{
      letter-spacing: 0.0175em;
      font-size: .875em;
    }
    ._view2{
      display: none;
    }
    svg{
      width: 1.3125em;
      margin-left: .44em;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }
}

.gray-bg{
  background: #FBFBFC;
}

.line-title{
  position: relative;
  padding-bottom: .56em;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 2.75em;
    height: 0.25em;
    background-color: #C33E35;
  }
}

.categories{
  &__items{
    margin-top: 2.5em;
    @media screen and (min-width: 993px){
      .swiper{
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
          gap: 1.875em;
        }
        &-slide{
          width: 100%;
          height: 100%;
        }
        &-scrollbar{
          display: none;
        }
      }
    }
  }
}

.category-card{
  display: flex;
  flex-direction: column;
  border-radius: 0.1875em;
  border: $border;
  outline: 1px solid transparent !important;
  background-color: #fff;
  padding: 2.91em;
  &:hover{
    border-color: $primary;
    outline-color: $primary !important;
    color: $black;
  }
  &:not(:hover){
    @media screen and (min-width: 993px){
      .category-card__link{
        span{
          transform: scale(0);
          width: 0;
        }
        svg{
          margin-left: 0;
        }
        path{
          fill: #D7DADA;
        }
      }
    }
  }
  &__title{
    display: block;
    min-height: 5.4375em;
    @include truncate(3);
  }
  &__img{
    margin-top: 1.56em;
    height: 15.125em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-family: $font2;
    color: $primary;
    svg{
      width: 2.875em;
      height: auto;
      margin-left: .625em;
      flex-shrink: 0;
    }
    span{
      transform: inherit;
      font-size: 1.0625em;
      line-height: 170%;
    }
  }
}


@media screen and (max-width: 992px){
  .main-slider{
    margin-bottom: 1.63em;
    .swiper-pagination{
      left: 1.56em;
      bottom: 3.5em;
      max-width: 20.625em;
    }
    .swiper-nav{
      display: none !important;
    }
  }

  .main-slide{
    background-image: url(../img/main-bg2.webp);
    position: relative;
    &:nth-child(even){
      background-image: url(../img/main-bg4.webp);
    }
    &__inner{
      position: static;
    }
    &__img{
      width: 27.5625em;
      max-width: calc(100% - 20.625em);
      img{
        max-height: 100%;
      }
    }
    &__content{
      min-height: 23.8125em;
      padding: 3.125em 0 5.625em;
      max-width: 20.625em;
    }
    &__desc{
      margin-top: .81em;
    }
    &__nav{
      margin-top: .81em;
    }
  }

  .m-section{
    padding: 1.875em 0;
    &__link{
      padding: 0 1em;
      height: 2.125em;
    }
  }

  .line-title{
    padding-bottom: .875em;
  }

  .categories{
    padding: 3.125em 0;
    overflow: hidden;
    &__items{
      margin-top: 1.875em;
      .swiper{
        &-slide{
          width: 16.875em;
          padding-right: .9375em;
        }
        &-scrollbar{
          display: block;
        }
      }
    }
  }

  .category-card{
    padding: 1.25em;
    &__title{
      font-size: 1.0625em;
      line-height: 129%;
      min-height: 3.87em;
    }
    &__img{
      height: 11.4375em;
      margin-top: .81em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    margin-bottom: 0;
    .swiper-pagination{
      left: 0;
      bottom: 19.25em;
      width: 100%;
      justify-content: center;
      max-width: 100%;
    }
  }

  .main-slide{
    padding-bottom: 19.25em;
    &__img{
      position: absolute;
      bottom: -3.5em;
      top: unset;
      width: 100%;
      max-width: 100%;
      height: 19.25em;
      @include center;
    }
    &__content{
      padding: 3.06em 0 2.19em;
      text-align: center;
      min-height: unset;
    }
    &__nav{
      justify-content: center;
    }
  }

  .m-section{
    &__link{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .category{
    padding: 3.75em 0;
  }
}
