.compare{
  padding: 1.56em 0 7.5em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,25%);
    width: calc(100% + 1.875em);
    margin-top: 5.44em;
  }
}

.compare-filter{
  margin-top: 1.56em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .sort{
    .fg{
      width: 22.9375em;
      .nice-select{
        &:before{
          margin: 0;
        }
        .current{
          width: calc(100% - 3.6875em);
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        .option{
          white-space: pre-wrap;
          height: unset;
          min-height: 3.6875em;
        }
      }
    }
  }
}

.compare-sidebar{
  background-color: #fff;
  position: relative;
  z-index: 2;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: 100%;
    width: 2000px;
    height: 100%;
    background-color: #fff;
  }
  &__top{
    height: 25.69em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 2.19em;
    //border-bottom: 2px solid $lightGray;
  }
  &__set,
  &__props,
  &__available{
    border-top: 2px solid $lightGray;
    padding: 2.19em 0;
  }
  &__available{
    height: 10.75em;
  }
  &__props{
    font-weight: 500;
  }
}

.compare-show{
  &__items{
    margin-top: 1.06em;
    @include grid100gap(.75em);
  }
}

.compare-slider{
  grid-column: 2/-1;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.compare-item{
  &__props,
  &__set{
    border-top: 2px solid $lightGray;
    padding: 2.19em 0;
    .compare-props{
      margin-top: 1.6875em;
    }
  }
  &__set{
    padding-right: 2.5em;
    .product-info__items{
      margin-top: 0;
    }
  }
}

.compare-card{
  padding-right: 1.875em;
  height: 25.69em;
  &__top{
    position: relative;
  }
  &__img{
    display: block;
    @include imgCover(83.8%);
    border-radius: .1875em;
    border: $border2;
  }
  .m-rating{
    position: absolute;
    bottom: 1.25em;
    left: 1.25em;
    z-index: 2;
  }
  &__bottom{
    margin-top: .94em;
    display: grid;
    grid-template-columns: calc(100% - 2.9375em - .31em) 2.9375em;
    gap: .31em;
  }
  &__title{
    @include truncate(2);
  }
  .product-prices{
    margin-top: .34em;
    white-space: nowrap;
    &__old{
      font-size: .875em;
    }
    &__current{
      font-size: 1.1875em;
    }
  }
  &__nav{
    display: flex;
    align-items: flex-end;
  }
  .cart-btn{
    padding: 0;
    width: 2.9375em;
    height: 2.9375em;
    min-width: unset;
    img{
      margin: 0;
    }
  }
}

.compare-available{
  border-top: 2px solid $lightGray;
  padding: 2.19em 0;
  @include grid100gap(.625em);
  height: 10.75em;
  &__badge{
    display: flex;
    align-items: center;
    color: #1B9665;
    img{
      width: 1.0625em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    &._orange{
      color: #F59439;
    }
  }
}

.compare-props{
  padding: .94em 0;
}

.compare-row{
  height: 2.9375em;
  display: flex;
  align-items: center;
  padding: 0 .625em;
  &:nth-child(odd){
    background: #FAFAFA;
  }
  &__available span,
  &__item{
    font-size: .9375em;
    line-height: 150%;
  }
  &__available{
    display: flex;
    align-items: center;
    img{
      margin-right: .5em;
      flex-shrink: 0;
      width: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .compare{
    padding-bottom: 3.125em;
    &__grid{
      width: calc(100% + 1.25em);
      margin-top: 1.56em;
    }
  }

  .compare-sidebar{
    &__top{
      height: 16.9375em;
      padding-bottom: 1.26em;
    }
    .favourite__delete-title{
      font-size: .75em;
    }
    &__available{
      padding: 1.56em 0;
      height: 8.75em;
    }
    &__title{
      .text18{
        font-size: .875em;
        line-height: 135%;
      }
    }
    &__props{
      padding: .94em 0;
    }
    &__set{
      padding-top: 1.56em;
      padding-bottom: 0;
    }
  }

  .compare-show{
    &__items{
      .m-radio{
        label {
          padding-left: 1.62em;
          span{
            font-size: .875em;
          }
        }
      }
    }
  }

  .compare-item{
    &__props{
      padding: .94em 0;
      .compare-props{
        margin-top: 1.1875em;
      }
    }
    &__set{
      padding-top: 1.56em;
      padding-right: 1em;
      padding-bottom: 0;
      .product-info__items{
        li{
          font-size: .875em;
          line-height: 129%;
          padding-left: 1.1em;
        }
      }
    }
  }

  .compare-card{
    padding-right: 1.25em;
    height: 16.9375em;
    .m-rating{
      bottom: .625em;
      left: .625em;
    }
    &__bottom{
      height: 6.4375em;
      grid-template-columns: calc(100% - 2.125em) 2.125em;
    }
    &__info{
      display: flex;
      flex-direction: column;
    }
    &__title{
      font-size: .875em;
      line-height: 135%;
      min-height: 4.05em;
      -webkit-line-clamp: 3;
      width: calc(100% + 2.43em);
    }
    .product-prices{
      padding-top: .13em;
      margin-top: auto;
      flex-wrap: wrap;
      &__item{
        &:nth-child(2){
          order: 2;
          width: 100%;
        }
      }
      &__current{
        font-size: 1em;
      }
      .m-badge{
        padding: 0 .12em;
        height: 1.25em;
        span{
          font-size: .625em;
        }
      }
    }
    .cart-btn{
      width: 2.125em;
      height: 2.125em;
    }
  }

  .compare-available{
    padding: 1.56em 0;
    height: 8.75em;
    .product-advantages{
      &__item{
        img{
          width: 1.625em;
        }
      }
    }
  }

  .compare-row{
    padding: .3125em;
    height: 2.375em;
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(10),
    &:nth-child(11),
    &:nth-child(12),
    &:nth-child(14){
      @media screen and (min-width: 577px){
        height: 3.5em;
      }
    }
    &__available span,
    &__item{
      font-size: .875em;
      line-height: 128%;
    }
  }
}


@media screen and (max-width: 576px){
  .compare-filter{
    display: block;
  }

  .compare{
    &__grid{
      display: block;
      position: relative;
      margin-top: .94em;
    }
    .favourite__delete{
      margin-top: 5.87em;
    }
  }

  .compare-sidebar{
    //opacity: 0;
    top: 0;
    left: 0;
    position: absolute;
    height: 100%;
    width: calc(100% - 1.25em);
    z-index: unset;
    //pointer-events: none;
    background-color: transparent;
    .favourite__delete{
      display: none;
    }
    &__available{
      border: none;
      height: 10.8125em;
    }
    .compare-row{
      align-items: unset;
      padding-top: .625em;
    }
    &__set{
      padding-top: .94em;
    }
  }

  .compare-show{
    position: absolute;
    width: 100%;
    bottom: 100%;
    left: 0;
    margin-bottom: 4.06em;
    &__items{
      gap: unset;
      display: flex;
      align-items: center;
      margin-top: .94em;
      .m-radio{
        @include lastM(0 .625em 0 0);
      }
    }
  }

  .compare-slider{
    .swiper-slide{
      width: 50%;
    }
  }

  .compare-available{
    height: 10.8125em;
    padding: 4.25em 0 .94em;
  }

  .compare-item{
    .compare-row{
      padding-top: 2.69em;
      background-color: transparent !important;
    }
    &__set{
      padding-top: 2.75em;
    }
  }

  .compare-row{
    height: 4.4375em;
  }
}
