.cart{
  padding: 1.56em 0;
  &__nav{
    margin-top: 2.5em;
    display: flex;
    align-items: center;
  }
  &__all{
    label{
      padding-top: 0;
      span{
        font-size: .9375em;
        line-height: 173%;
      }
      &:before{
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  &__delete{
    display: flex;
    align-items: center;
    margin-left: 2.5em;
    color: rgba($black,.7);
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .625em;
    }
    span{
      transition: inherit;
      font-size: .9375em;
      line-height: 140%;
      border-bottom: 1px dotted rgba($black,.7);
    }
    &:hover{
      color: $primary;
      span{
        border-color: $primary;
      }
      path{
        stroke: $primary;
      }
    }
  }
  &__grid{
    margin-top: 1.56em;
    display: grid;
    grid-template-columns: calc(100% - 20.4375em - 1.875em) 20.4375em;
    gap: 1.56em 1.875em;
  }
}

.cart-items{
  @include grid100gap(.9375em);
}

.cart-row{
  padding: 1.25em 1.56em;
  border-radius: 0.25em;
  border: $border2;
  display: grid;
  grid-template-columns: calc(100% - 24.75em - 4.6875em) 9em 9em 6.75em;
  gap: 1.56em;
  align-items: center;
  &._disabled{
    background: #FBFBFB;
    .cart-row__action,
    .cart-row__sum,
    .cart-row__info{
      opacity: 0.6;
    }
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__checks{
    width: 1.25em;
    margin-right: 1.56em;
    flex-shrink: 0;
    label{
      padding-left: 0;
    }
  }
  &__img{
    display: block;
    width: 7.625em;
    height: 6.3125em;
    border-radius: 0.1875em;
    border: $border2;
    flex-shrink: 0;
    margin-right: .94em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 150%;
    font-weight: 500;
  }
  .product-prices{
    margin-top: .44em;
    .m-badge{
      padding: 0 .56em;
      min-width: unset;
    }
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__remove{
    width: 3em;
    height: 3em;
    @include center;
    border-radius: 0.1875em;
    background-color: $lightGray;
    margin-left: .9375em;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  .favourite-btn{
    width: 2.8125em;
    height: 2.8125em;
    border-radius: 0.1875em;
    border: $border2;
    &:hover{
      border-color: $black;
    }
    svg{
      width: 1.4375em;
    }
  }
  &__available{
    display: flex;
    align-items: center;
    color: $primary;
    img{
      width: 1.0625em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #E9EDF2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #E9EDF2;
    border-right: 1px solid #E9EDF2;
    color: $black;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}

.cart-sum{
  position: sticky;
  top: 1em;
  padding: 1.56em;
  border-radius: 0.1875em;
  box-shadow: 0 .25em 1.6875em 0 rgba(0, 0, 0, 0.07);
  @include grid100gap(1.25em);
  &__props{
    @include grid100gap(.9375em);
  }
  &__btn{
    width: 100%;
    height: 3.4375em;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__hint{
    border-radius: 0.1875em;
    border: 1px dashed #7EACB8;
    background: #F6FBFC;
    padding: .625em;
    color: $textGray;
  }
  &__nav{
    @include grid100gap(.625em);
  }
  &__info{
    &-item{
      padding: .625em 0;
      @include grid100gap(.19em);
      border-bottom: $border2;
    }
    &-title{
      font-size: 1em;
      line-height: 169%;
      font-weight: 500;
    }
    &-desc{
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    color: #2C333D;

    &._big{
      font-weight: 500;
      font-size: 1.125em;
      line-height: 132%;
    }
  }
  &__desc{
    order: 2;
    &._big{
      font-size: 1.375em;
      font-weight: 500;
    }
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
}

.promo-code{
  @include grid100gap(.375em);
  .text15{
    line-height: 140%;
  }
  .fg{
    input{
      height: 3.1875em;
    }
  }
  &__btn{
    height: 3.1875em;
    border-width: 2px;
    font-weight: 500;
    color: $blue;
    span{
      font-size: .9375em;
    }
  }
  &__message{
    border-radius: 0.1875em;
    border: 2px solid $blue;
    height: 3.1875em;
    @include center;
    color: #1B9665;
    font-weight: 500;
    img{
      margin-left: .625em;
      width: 1em;
    }
  }
  &._error,
  &._error2{
    .fg{
      input{
        border-color: $primary;
        color: $primary;
      }
    }
  }
  &._error{
    .promo-code__message{
      color: $primary;
      border-color: #F0D2D0;
      img{
        width: 1.5em;
      }
    }
  }
  &._error2{
    .promo-code__message{
      color: $blue;
    }
  }
}

.cart-empty{
  padding-bottom: 7.5em;
  &__inner{
    @include grid100gap(1em);
    margin-top: 2.5em;
    border-radius: 0.1875em;
    border: 1px dashed #99B8C0;
    background: #FBFCFD;
    text-align: center;
    padding: 2.5em 1em;
  }
  &__img{
    width: 7.8125em;
    padding-top: .5em;
    margin: 0 auto .75em;
    position: relative;
    &._small{
      ._pin{
        top: 0;
      }
    }
    img{
      width: 100%;
      display: block;
    }
    ._pin{
      position: absolute;
      top: 1.5em;
      right: .7em;
      transform: translate(1.19em,-50%);
      width: 2.6em;
      height: 2.6em;
      span{
        font-size: .875em;
      }
    }
  }
  .text17{
    color: #383838;
    line-height: 141%;
    a{
      font-weight: 700;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    .m-btn{
      height: 3.6875em;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__nav{
      margin-top: 1.56em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__all{
      label{
        padding-left: 1.625em;
        span{
          font-size: .875em;
        }
      }
    }
    &__delete{
      svg{
        margin-right: .375em;
      }
      span{
        font-size: .875em;
      }
    }
  }

  .cart-row{
    padding: 1.56em;
    grid-template-columns: calc(100% - 20.1825em)  10.5em 6.5625em;
    &__info{
      grid-column: 1/-1;
    }
    &__img{
      margin-right: 1.56em;
    }
    &__title{
      font-size: .875em;
      line-height: 136%;
    }
    &__count{
      width: 10em;
      margin-left: auto;
      .cartcalc{
        width: 10em;
      }
    }
    .product-prices{
      .m-badge{
        height: 1.625em;
      }
      &__old{
        font-size: 1.0625em;
      }
      &__current{
        font-size: 1.375em;
      }
    }
    &__remove{
      width: 2.8125em;
      height: 2.8125em;
    }
  }

  .cart-empty{
    padding-bottom: 3.125em;
    &__inner{
      margin-top: 1.56em;
    }
    &__img{
      ._pin{
        width: 2.12em;
        height: 2.12em;
        span{
          font-size: .71em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart-row{
    padding: .9375em .9375em .9375em 3.125em;
    grid-template-columns: 8em calc(100% - 9.56em);
    row-gap: .9375em;
    &__info{
      display: block;
      padding-left: 2.19em;
      width: calc(100% + 2.19em);
      margin-left: -2.19em;
      position: relative;
    }
    &__check{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &__img{
      margin: 0 0 .625em;
    }
    &__title{
      font-size: .875em;
      line-height: 136%;
    }
    &__count{
      width: auto;
      margin: 0;
      .cartcalc{
        width: 100%;
        margin-left: auto;
      }
    }
    .product-prices{
      &__old{
        font-size: .875em;
      }
      &__current{
        font-size: 1.125em;
      }
    }
    &__action{
      justify-content: unset;
    }
  }

  .cart-sum{
    padding: .9375em;
    gap: .9375em;
  }

  .cart-empty{
    &__img{
      width: 6.5em;
    }
  }
}
