.catalog-detail{
  margin-top: 3.125em;
  .tab-content{
    margin-top: 2.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 20.4375em - 1.94em) 20.4375em;
    gap: 1.94em;
  }
  .props{
    margin-top: 1.25em;
  }
  .pagination{
    margin-top: 2.5em;
    &__items{
      justify-content: unset;
    }
    &__item{
      &:first-child{
        margin-left: 0;
      }
    }
  }
}

.tabs{
  display: flex;
  border-bottom: $border;
}

.tab{
  height: 3.5625em;
  padding: 0 1.56em;
  display: flex;
  align-items: center;
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  color: rgba($black,.8);
  transition: all .3s;
  @include lastM(0 .94em 0 0);
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: $primary;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: .875em;
    line-height: 120%;
  }
  &:hover{
    background: #FBFBFB;
    cursor: pointer;
  }
  &._active{
    background: #FBFBFB;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.props{
  max-width: 42.8125em;
  @include grid100gap(1em);
}

.reviews{
  margin-top: 3.75em;
  &__items{
    @include grid100gap(1.875em);
    margin-top: 1.25em;
  }
  &__more{
    margin-top: 1.875em;
    height: 2.5em;
    font-weight: 500;
    span{
      font-size: .875em;
    }
    svg{
      width: 1.375em;
      height: auto;
      margin-left: .44em;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }
}

.review-card{
  padding: .625em 0 1.25em;
  @include grid100gap(1.06em);
  border-bottom: $border2;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name{
    font-size: 1.4375em;
    line-height: 113%;
    font-weight: 500;
  }
  &__date{
    font-size: .8125em;
    color: #7D8A96;
  }
  &__rating{
    display: flex;
    align-items: center;
    .m-rating{
      margin-right: .56em;
    }
  }
}

.rating{
  display: flex;
  &__title{
    margin-right: .6875em;
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: .9375em;
    &:not(:last-child){
      margin-right: .18em;
    }
    svg{
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $yellow;
      }
      &~.rating-input__item {
        path{
          fill: $yellow;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $yellow;
  }
}

.review-form{
  margin-top: 3.75em;
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: 1.25em;
  }
  &__bottom{
    display: flex;
  }
  .fg{
    textarea{
      height: 6.5625em;
    }
  }
  &__btn{
    width: unset;
    padding: 0 2.5em;
  }
}

.rating-field{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  label{
    margin-bottom: .67em;
  }
  .rating-input{
    border-radius: 0.25em;
    border: $border2;
    &__item{
      width: 1.9375em;
      height: 1.9375em;
      @include center;
      margin: 0 !important;
      &:not(:first-child){
        border-right: $border2;
      }
      svg{
        width: .9375em;
      }
    }
  }
}

.brand{
  padding: 1.56em;
  border-radius: 0.1875em;
  background-color: #fff;
  box-shadow: 0 .25em 1.6875em 0 rgba(0, 0, 0, 0.07);
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 6.5625em;
    min-height: 3.25em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-right: 1em;
    img{
      display: block;
      width: 100%;
    }
  }
  &__info{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title{
    text-transform: uppercase;
    font-weight: 700;
    line-height: 140%;
  }
  &__link{
    margin-top: .19em;
    color: #2D2C2C;
    border-bottom: 1px dotted #2D2C2C;
    &:hover{
      color: $primary;
      border-color: $primary;
    }
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
  &__country{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
    img{
      width: 1.8125em;
      flex-shrink: 0;
      margin-right: .875em;
    }
  }
}

.product-info{
  margin-top: 2.5em;
  padding: 1.56em;
  border-radius: 0.1875em;
  border: $border2;
  @include grid100gap(1.875em);
  &__items{
    margin-top: .94em;
    @include grid100gap(.625em);
    li{
      font-size: 0.9375em;
      line-height: 140%;
      padding-left: 1.33em;
      background-image: url(../img/check5.svg);
      background-repeat: no-repeat;
      background-size: .8em auto;
      background-position: left 0 top .25em;
    }
  }
  .props{
    margin-top: .94em;
  }
}

.product-sidebar{
  position: sticky;
  top: 1em;
}

.product-videos{
  @include grid100gap(1.25em);
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.video{
  display: block;
  position: relative;
  &__img{
    border-radius: .1875em;
    &:after{
      background: linear-gradient(0deg, rgba(11, 19, 21, 0.40) 0%, rgba(11, 19, 21, 0.40) 100%);
    }
    img{
      border-radius: inherit;
      width: 100%;
      display: block;
    }
  }
  .play-btn{
    @include centerAbsolute;
  }
  &._small{
    .play-btn__round{
      width: 3.9375em;
      height: 3.9375em;
    }
  }
}

.play-btn{
  display: flex;
  align-items: center;
  &__round{
    width: 5.6875em;
    height: 5.6875em;
    @include center;
    border-radius: 50%;
    flex-shrink: 0;
    img{
      width: 1.5em;
    }
  }
  &__text{
    font-size: 1.0625em;
    margin-left: .94em;
    font-weight: 500;
    line-height: 123%;
    color: #fff;
  }
}

.leasing{
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
}

.leasing-advantages{
  margin: 1.25em 0;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  gap: 1.25em;
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 2.5em;
      flex-shrink: 0;
      margin-right: 1.06em;
    }
    .text16{
      line-height: 131%;
    }
  }
}

.review-auth{
  margin-top: 3.75em;
  @include grid100gap(1.25em);
  .info-message{
    min-height: 4.375em;
  }
}


@media screen and (max-width: 992px) {
  .catalog-detail {
    .tab-content {
      margin-top: 3.125em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.56em;
      &._hide-tablet{
        display: none;
      }
    }
    &__sidebar{
      order: -1;
    }
    .props{
      margin-top: .94em;
    }
    .pagination{
      margin-top: 1.56em;
    }
  }

  .tab {
    padding: 0 .94em;
    height: 2.3125em;
  }

  .props{
    gap: .625em;
    .prop-item{
      &__title,
      &__desc{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }

  .reviews{
    margin-top: 3.125em;
    &__items{
      gap: 1.56em;
    }
    &__more{
      margin-top: 1.56em;
    }
  }

  .review-card{
    padding-bottom: .94em;
    gap: .94em;
    &__name{
      font-size: 1.25em;
      line-height: 115%;
    }
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .review-form{
    margin-top: 3.125em;
  }

  .product-sidebar{
    position: static;
  }

  .brand{
    max-width: 19.8125em;
  }

  .product-info{
    margin-top: 1.25em;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    &__title{
      font-size: .875em;
      line-height: 136%;
    }
    .props{
      gap: 1em;
    }
  }

  .video{
    &._small{
      .play-btn__round{
        width: 2.5em;
        height: 2.5em;
        img{
          width: .75em;
        }
      }
    }
  }

  .play-btn{
    display: flex;
    align-items: center;
    &__round{
      width: 4.4375em;
      height: 4.4375em;
      img{
        width: 1.375em;
      }
    }
  }

  .leasing-advantages{
    &__item{
      .text16{
        font-size: .875em;
        line-height: 143%;
        font-weight: 400;
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .catalog-detail{
    .pagination{
      margin-top: 1.25em;
      &__items{
        justify-content: center;
      }
      &__item{
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }

  .tabs {
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    padding: 0 .9375em;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  .tab {
    white-space: nowrap;
    flex-shrink: 0;
    height: 1.75em;
    background-color: transparent !important;
    padding: 0 .625em;
  }

  .props{
    .prop-item{
      &__title{
        max-width: 15em;
      }
    }
  }

  .reviews{
    margin-top: 1.56em;
    &__more{
      margin-top: .94em;
    }
  }

  .review-form{
    margin-top: 1.56em;
    &__fields{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }

  .brand{
    max-width: 100%;
  }

  .product-info{
    padding: .94em;
    gap: .94em;
    grid-template-columns: 100%;
  }

  .play-btn{
    &__round{
      width: 2.5em;
      height: 2.5em;
      img{
        width: .75em;
      }
    }
    &__text{
      display: none;
    }
  }

  .leasing-advantages{
    grid-template-columns: 100%;
    br{
      display: none;
    }
  }
}
