.auth{
  padding: 1.56em 0 5em;
  &__inner{
    margin-top: 2.5em;
    padding: 2.5em;
    border-radius: 0.1875em;
    border: $border2;
    display: grid;
    grid-template-columns: 29em 42.6875em;
    justify-content: space-between;
    align-items: center;
    gap: 1.56em;
  }
  .info-message{
    margin-top: 2.5em;
  }
}

.login-form{
  @include grid100gap(1.56em);
  &__remember{
    display: inline-block;
    margin: 0 auto;
  }
  &__hint{
    text-align: center;
  }
  &._type2{
    padding: 2.5em;
    border-radius: 0.1875em;
    border: $border2;
    margin: 2.5em auto 0;
    width: 34em;
    max-width: 100%;
  }
}

.m-link{
  @include center;
  height: 2.5625em;
  border-radius: 0.1875em;
  background-color: $lightGray;
  &:hover{
    background-color: $primary;
    color: #fff;
  }
  span{
    font-size: .9375em;
    line-height: 140%;
    text-decoration: underline;
  }
}

.login-info{
  height: 100%;
  display: flex;
  padding-left: 3.125em;
  border-left: $border2;
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1.81em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    min-height: 2.9375em;
    display: flex;
    align-items: center;
    font-weight: 800;
  }
  .text{
    margin-top: 1.56em;
  }
  &__nav{
    display: flex;
    margin-top: 1.56em;
  }
}

.reg{
  .auth__inner{
    align-items: flex-start;
  }
  .login-info{
    &__nav{
      margin-top: .625em;
    }
  }
}

.info-message{
  padding: .625em;
  display: flex;
  align-items: center;
  border-radius: 0.1875em;
  border: 1px dashed #7EACB8;
  background: #F6FBFC;
  color: $blue;
  &._center{
    justify-content: center;
  }
  &._big{
    @media screen and (min-width: 993px){
      padding: 1.81em;
    }
  }
  a{
    text-decoration: underline;
  }
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .625em;
  }
}


@media screen and (max-width: 992px){
  .auth{
    &__inner{
      margin-top: 1.56em;
      padding: 1.56em;
      grid-template-columns: 100%;
    }
    .info-message{
      margin-top: 1.56em;
    }
  }

  .login-form{
    &._type2{
      margin-top: 1.56em;
      padding: 1.56em;
      width: 32.125em;
    }
  }

  .login-info{
    padding: 1.56em 0 0;
    border-left: none;
    border-top: $border2;
    position: relative;
    &__icon{
      width: 2.375em;
      top: 1.56em;
      left: 0;
      position: absolute;
    }
    &__top{
      min-height: 2.8125em;
      padding-left: 3.81em;
    }
  }

  .info-message{
    padding: .9375em .625em;
  }
}


@media screen and (max-width: 576px){
  .auth{
    &__inner{
      padding-left: .9375em;
      padding-right: .9375em;
    }
  }

  .login-form{
    gap: .9375em;
    &._type2{
      padding: .9375em;
    }
  }

  .login-info{
    .text{
      margin-top: .9375em;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
}
