.header{
  position: relative;
  z-index: 10;
  padding-bottom: 3.1875em;
  border-bottom: 1px solid #f3f6f6;
  &__logo{
    display: block;
    width: 11.875em;
    flex-shrink: 0;
    margin-right: 5.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__catalog{
    height: 3.125em;
    padding: 0 1.38em;
    font-weight: 700;
    span{
      font-size: .9375em;
    }
    img{
      width: 1.5em;
      margin-right: .625em;
    }
  }
}

.header-top{
  padding: .625em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

._flex{
  display: flex;
  align-items: center;
  svg{
    height: auto;
  }
}

.header-city{
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .31em;
  }
  &__desc{
    margin-left: .69em;
    color: #404748;
  }
}

.header-contacts{
  &__phone,
  &__mail{
    svg{
      margin-right: .375em;
      width: 1.25em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__phone{
    font-weight: 500;
    margin-left: .69em;
    svg{
      width: 1em;
    }
  }
  &__time{
    margin-left: 2.81em;
    color: #404748;
  }
  &__call{
    margin-left: .81em;
    color: $primary;
    font-weight: 500;
    text-decoration: underline;
    &:hover{
      text-decoration-color: transparent;
    }
    ._view2{
      display: none;
    }
  }
}

.header-auth{
  color: #404748;
  &__icon{
    width: 1.125em;
    margin-right: .5em;
  }
}

.header-center{
  padding: .94em 0;
  border-top: 1px solid #F3F6F6;
}

._pin{
  min-width: 1.0625em;
  height: 1.0625em;
  padding: 0 .125em;
  background-color: $primary;
  color: #fff;
  @include center;
  border-radius: 50%;
  font-weight: 500;
  span{
    font-size: .625em;
    line-height: 120%;
  }
}

.header-search{
  flex-grow: 1;
  margin: 0 4em 0 1.69em;
  &__close{
    display: none;
  }
  &._show{
    .search-field{
      &:before{
        opacity: 1;
      }
    }
    .search-field__btn{
      background-color: $blue;
      path{
        stroke: #fff;
      }
    }
  }
}

.m-search{
  position: relative;
  &__result{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    box-shadow: 0 .25em 1.125em 0 rgba(0, 0, 0, 0.25);
    border-radius: 0 0 .1875em .1875em;
  }
  &__items{
    @include scroll;
    overflow-y: auto;
    max-height: 25em;
  }
  &__link{
    height: 3em;
    font-weight: 500;
    background: #F6FBFC;
    border-radius: 0 0 .1875em .1875em;
    &:hover{
      background: $blue;
      color: #fff;
    }
    span{
      font-size: 0.875em;
    }
  }
  &._show{
    z-index: 3;
    .m-search__result{
      opacity: 1;
      pointer-events: unset;
    }

  }
}

.search-field{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: .1875em;
    border: .1875em solid $blue;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__btn{
    width: 3.0625em;
    height: 100%;
    @include center;
    position: absolute;
    right: 1px;
    top: 50%;
    transform: translateY(-50%);
    @include center;
    svg{
      width: 1.1875em;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  input{
    border: 1px solid #D2D2DA;
    width: 100%;
    padding: 0 3.5em 0 1em;
    height: 3.33em;
    font-size: .9375em;
    border-radius: .23em;
    line-height: 120%;
    font-family: $font1;
    &::placeholder{
      color: #8D999C;
    }
    &:focus{
      border-color: $primary;
    }
  }
}

.search-item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .9375em 1.56em;
  border-bottom: $border;
  &:hover{
    color: $black;
    background: #FAFAFA;
  }
  &__content{
    @include grid100gap(.3125em);
  }
  &__title{
    @include truncate(1);
    span{
      font-weight: 700;
    }
  }
  &__price{
    font-weight: 500;
    color: $blue;
  }
  &__btn{
    height: 2.1875em;
    flex-shrink: 0;
    padding: 0 1.38em;
    font-weight: 700;
    text-transform: uppercase;
    span{
      font-size: .75em;
      line-height: 110%;
    }
    svg{
      display: none;
    }
  }
}

.empty-search{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: .94em 1.56em;
  color: $blue;
  &__text{
    line-height: 147%;
    b{
      font-weight: 700;
    }
    a{
      text-decoration: underline;
    }
  }
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .625em;
  }
}

.header-action{
  display: flex;
  &__btn{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    color: #6B7076;
    @include lastM(0 1.56em 0 0);
    &:hover{
      color: $primary;
      line,
      path{
        stroke: $primary;
      }
    }
    svg{
      height: 1.625em;
      width: auto;
      margin-bottom: .125em;
    }
    ._pin{
      right: -.25em;
      top: -.38em;
      position: absolute;
    }
    &-icon{
      position: relative;
    }
    &._hide-desktop{
      @media screen and (min-width: 993px){
        display: none;
      }
    }
  }
}

.menu{
  padding: .94em 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  &__catalog{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__link{
    span{
      font-size: .9375em;
      line-height: 140%;
    }
    &:hover{
      color: #C33E35;
    }
  }
}


.mob-menu__close,
.mob-menu__bg,
.header__mob-call,
.burger,
.tablet-menu{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding-bottom: 3em;
    &__logo{
      width: 11.8125em;
      margin-right: 1.125em;
    }
    &__catalog{
      height: 2.625em;
      padding: 0 .625em;
      span{
        font-size: .875em;
      }
      img{
        margin-right: .375em;
      }
    }
  }

  .header-action{
    &__btn{
      @include lastM(0 1.25em 0 0);
      .text13{
        display: none;
      }
      svg{
        height: 1.5em;
        margin: 0;
      }
    }
  }

  .header-center{
    padding: 1.06em 0;
  }

  .header-search{
    margin: 0 .94em;
    @media screen and (min-width: 577px){
      height: 2.625em;
      &._show{
        .search-field{
          position: absolute;
          top: 0;
          left: 0;
          width: calc(100vw - 9.25em);
          margin-left: -17.5em;
        }
        .m-search__result{
          width: calc(100vw - 9.25em);
          margin-left: -17.5em;
        }
        .header-search__close{
          opacity: 1;
          pointer-events: unset;
        }
        &~.header-action{
          opacity: 0;
        }
      }
      &__close{
        opacity: 0;
        pointer-events: none;
        position: absolute;
        top: 0;
        right: -12em;
        display: flex;
        width: 2.625em;
        height: 2.625em;
        padding: 0;
        &:hover{
          path{
            stroke: #fff;
          }
        }
        svg{
          width: 1.125em;
        }
      }
    }
  }

  .search-field{
    input{
      height: 2.8em;
    }
  }

  .menu{
    padding: .875em 0;
  }

  .nav{
    &__link{
      span{
        font-size: .875em;
      }
      &._hide-tablet{
        @media screen and (min-width: 577px){
          display: none;
        }
      }
    }
  }

  .tablet-menu{
    display: block;
    position: relative;
    &._show{
      .tablet-menu__links{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      display: flex;
      align-items: center;
      cursor: pointer;
      img{
        width: 1.5em;
        margin-left: .25em;
        flex-shrink: 0;
      }
    }
    &__links{
      position: absolute;
      top: 100%;
      right: 0;
      box-shadow: 0 .25em 1.125em 0 rgba(0,0,0,.25);
      background-color: #fff;
      margin-top: .25em;
      padding: .5em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      white-space: nowrap;
      border-radius: .1875em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    .nav__link{
      @include lastM(0 0 .5em);
    }
  }

  .header-auth{
    display: none;
  }

  .header-city{
    &__desc{
      display: none;
    }
  }

  .header-contacts{
    &__call{
      margin-left: .44em;
    }
    &__phone{
      margin-left: 2.81em;
    }
    &__time{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 0;
    &__logo{

    }
    &__catalog{
      display: none;
    }
  }

  .header-center{
    padding: .94em 0;
    &__inner{
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  .header-search{
    width: 100%;
    margin: .94em auto 0;
  }

  .header-action{
    background: #FFF;
    box-shadow: 0 -.0625em .6875em 0 rgba(0, 0, 0, 0.14);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    padding: .75em .94em;
    justify-content: space-between;
  }

  .header__mob-call,
  .burger{
    @include center;
    width: 1.5em;
    img{
      width: 100%;
    }
  }

  .header__mob-call{
    width: 1.25em;
  }

  .search-field{
    &:before{
      border-width: .125em;
    }
  }

  .search-item{
    padding: .625em .94em;
    &__title{
      font-size: .875em;
    }
    &__price{
      font-size: .9375em;
    }
    &__btn{
      margin-left: .94em;
      padding: 0;
      width: 2.625em;
      height: 2.625em;
      @include center;
      span{
        display: none;
      }
      svg{
        width: .5em;
        height: auto;
        display: block;
      }
      &:hover{
        path{
          stroke: #fff;
        }
      }
    }
  }

  .mob-menu{
    @include menu;
    padding: 0;
    .container{
      padding: 0;
    }
  }

  ._open-mob-menu{
    .mob-menu{
      transform: translateX(0);
      &__bg{
        display: block;
      }
      &__close{
        display: flex;
      }
    }
  }

  .header-top{
    &__inner{
      flex-direction: column;
    }
  }

  .header-city{
    padding: .94em;
    background: #F7FAFC;
    border-bottom: $border2;
    width: 100%;
    &__desc{
      display: block;
    }
  }

  .menu{
    position: static;
    padding: 0;
    &__catalog{
      display: flex;
      align-items: center;
      padding: .94em;
      min-height: 3.3125em;
      border-bottom: $border2;
      justify-content: space-between;
      font-weight: 700;
      svg{
        width: .4375em;
      }
    }
  }

  .nav{
    display: block;
    &__link{
      display: flex;
      align-items: center;
      padding: .94em;
      min-height: 3.3125em;
      border-bottom: $border2;
    }
  }

  .tablet-menu{
    display: none;
  }

  .header-contacts{
    padding: 1.56em;
    flex-direction: column;
    text-align: center;
    width: 100%;
    &__time{
      display: block;
      margin: 0 0 .69em;
    }
    &__mail{
      order: 4;
    }
    &__phone{
      margin: 0 0 1em;
    }
    &__call{
      text-decoration: none;
      background-color: $primary;
      color: #fff;
      width: 100%;
      height: 2.4375em;
      text-transform: uppercase;
      border-radius: 0.1875em;
      font-weight: 700;
      margin: 0 0 1em;
      &:hover{
        background-color: #C33E35;
        color: #fff;
      }
      span{
        font-size: .6875em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
