.footer{
  background: #000;
  color: #fff;
  &__top{
    padding: .94em 0;
  }
  &__logo{
    display: block;
    width: 11.875em;
    flex-shrink: 0;
    margin-right: 3.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c,
  &__inn{
    color: #A4B4BC;
    margin-right: 2.44em;
  }
  .socials{
    margin-left: auto;
  }
  &__center{
    display: flex;
    justify-content: space-between;
    padding: 3.125em 0;
  }
  &__title{
    opacity: 0.4;
    font-size: 0.875em;
    font-weight: 700;
    line-height: 140%;
    text-transform: uppercase;
  }
  &__bottom{
    padding: 1.25em 0;
    border-top: 1px solid rgba(255, 255, 255, 0.06);
    text-align: center;
  }
  &__protect{
    color: #9A9FA0;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: #1A1A1A;
    @include lastM(0 .75em 0 0);
    @include center;
    &:hover{
      border-color: rgba(255, 255, 255, 0.50);
      background: transparent;
      path{
        fill-opacity: 1;
      }
    }
  }
}

.footer-nav{
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: .625em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .625em);
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
}

.footer-contacts{
  &__inner,
  &__mail,
  &__phone{
    margin-top: .625em;
  }
  &__time{
    margin-top: .625em;
    color: rgba(255, 255, 255, 0.50);
    ._view2{
      display: none;
    }
  }
  &__call{
    margin-top: .9375em;
    color: rgba(255, 255, 255, 0.50);
  }
  &__btn{
    margin-top: .625em;
    min-width: 16.3125em;
    width: 100%;
    height: 2.4375em;
    text-transform: uppercase;
    font-weight: 700;
    span{
      font-size: .6875em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__logo{
      margin-right: 1.56em;
      width: 9.3125em;
    }
    &__info{
      display: block;
    }
    &__c,
    &__inn{
      margin-right: 0;
    }
    &__center{
      padding: 1.25em 0;
      display: grid;
      grid-template-columns: 1fr 1fr .9fr 1.27fr;
      gap: 1.4em;
    }
  }

  .footer-nav{
    &__link{
      span{
        font-size: .8125em;
      }
    }
  }

  .footer-contacts{
    grid-column: 1/-1;
    @media screen and (min-width: 577px){
      .footer__title{
        text-align: center;
      }
      &__inner{
        display: grid;
        grid-template-columns: 18.9375em 12.0625em calc(100% - 32.25em);
        gap: .625em;
      }
      &__time{
        margin-top: 0;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
      &__phone{
        margin-top: .3125em;
      }
      &__call{
        display: none;
      }
      &__btn{
        min-width: unset;
        width: 100%;
        padding: 0;
        margin-top: .22em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      justify-content: space-between;
      flex-wrap: wrap;
    }
    &__info{
      width: calc(100% - 11.2em);
    }
    .socials{
      width: 100%;
      margin: .625em 0 0;
      justify-content: center;
    }
    &__c,
    &__inn{
      span{
        font-size: .75em;
      }
    }
    &__center{
      padding: .94em 0;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__bottom{
      padding: .625em 0;
    }
    &__protect{
      font-size: .8125em;
    }
  }

  .footer-nav{
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding: .75em 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba(255, 255, 255, 0.11);
    }
    &__btn{
      @include center;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.08);
      width: 1.25em;
      height: 1.25em;
      svg{
        width: .5em;
      }
    }
    &__hidden{
      display: none;
      padding-bottom: .625em;
    }
  }

  .footer-contacts{
    margin-top: .625em;
    &__phone{
      margin-top: .3125em;
    }
    &__call{
      text-align: center;
    }
    &__btn{
      min-width: unset;
    }
  }
}
