.catalog{
  padding: 1.56em 0 7.5em;
  &__grid{
    display: grid;
    grid-template-columns: 20.4375em calc(100% - 20.4375em - 1.875em);
    gap: 1.875em;
    margin-top: 2.5em;
  }
  .category-card{
    padding: 2.19em 1.56em;
    &__title{
      font-size: 1.0625em;
      line-height: 159%;
      min-height: 4.74em;
      br{
        display: none;
      }
    }
    &__nav{
      margin-top: 0;
    }
  }
}

.catalog-nav{
  border-radius: 0.1875em;
  background-color: $lightGray;
  overflow: hidden;
}

.nav-group{
  &__show{
    display: flex;
    align-items: flex-start;
    padding: 1.16em 1.875em;
  }
  &__toggle{
    width: 1.1875em;
    flex-shrink: 0;
    @include center;
    margin-right: .5em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 140%;
    font-weight: 500;
  }
  &__hidden{
    padding: .94em 2.19em;
    background: #FCFCFC;
    display: none;
  }
  &._open{
    .nav-group__show{
      background-color: $primary;
      color: #fff;
    }
    .nav-group__title{
      &:hover{
        opacity: .8;
        color: #fff;
      }
    }
    .nav-group__toggle{
      path{
        stroke: #fff;
      }
      ._plus{
        opacity: 0;
      }
    }
  }
  @media screen and (min-width: 993px){
    .category-card{
      border: none;
      padding: 0;
      outline: none !important;
      background-color: transparent;
      @include lastM(0 0 .81em);
      &__img,
      &__nav{
        display: none;
      }
      &__title{
        min-height: unset;
        font-size: 0.9375em;
        line-height: 140%;
        font-weight: 400;
        font-family: $font1;
      }
    }
  }
}

.catalog2{
  padding: 1.56em 0 7.5em;
  &__grid{
    display: grid;
    grid-template-columns: 20.5em calc(100% - 22.375em);
    gap: 1.56em 1.875em;
    margin-top: 2.5em;
  }
  .info-message{
    margin-top: 2.31em;
  }
}

.filter{
  border-radius: 0.1875em;
  border: $border;
  &__nav,
  &__bg,
  &__close{
    display: none;
  }
}

.filter-group{
  padding: 1.56em;
  &:not(:last-child){
    border-bottom: $border;
  }
  &__bottom{
    margin-top: 1.06em;
  }
  &__checks{
    @include grid100gap(.75em);
    width: calc(100% + .9375em);
    padding-right: .625em;
    @include scroll;
    max-height: 16.25em;
    overflow-y: auto;
  }
}

.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #D7DCDD;
    border: none;
    box-shadow: none;
    height: .1875em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $black;
      width: .9375em;
      height: .9375em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: .3125em 0 .6875em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.06em;
    &-title{
      position: absolute;
      top: 50%;
      left: 1.25em;
      transform: translateY(-50%);
    }
  }
  .fg{
    position: relative;
    input{
      height: 3.43em;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      padding-left: 2.85em;
      border-radius: .214em;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .9375em;
      line-height: 120%;
      color: #A7AAAE;
    }
  }
}

.filter-toggle{
  display: none;
}

.filter2{
  justify-content: space-between;
}

.sort{
  &__title{
    margin-right: .81em;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  .fg{
    width: 20.0625em;
  }
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
  margin-top: 1.25em;
  width: calc(100% + .625em);
  .selected-item{
    margin: .625em .625em 0 0;
  }
}

.selected-item{
  display: flex;
  align-items: center;
  padding: 0 1.25em;
  height: 2.8125em;
  border-radius: 0.1875em;
  border: 1px solid #F2F4F5;
  color: $black;
  font-weight: 500;
  &._all{
    background: #F6F8FB;
    border-color: #F6F8FB;
    color: $blue;
    &:hover{
      background: $blue;
      border-color: $blue;
      color: #fff;
      path{
        opacity: 1;
        stroke: #fff;
      }
    }
  }
  span{
    font-size: .875em;
    line-height: 110%;
  }
  &__remove{
    margin-left: .625em;
    width: .5625em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 100%;
      height: 100%;
    }
    &:hover{
      path{
        opacity: 1;
        stroke: $primary;
      }
    }
  }
}

.catalog-items{
  margin-top: 1.875em;
}

.manufacture-info{
  margin-top: 1em;
  padding: 1.56em;
  border: $border2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__img{
    margin-left: 1.875em;
    border-radius: 0.1875em;
    background-color: #fff;
    box-shadow: 0 .25em 1.6875em 0 rgba(0, 0, 0, 0.05);
    padding: .94em;
    width: 8.4375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding-bottom: 3.125em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
      margin-top: 1.56em;
    }
    &__content{
      display: none;
    }
  }

  .catalog-nav{
    padding: 0;
  }

  .nav-group{
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .catalog2{
    padding: 1.56em 0 3.125em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.56em;
      gap: 0;
    }
    .info-message{
      margin-top: 1.56em;
    }
  }

  ._open-filter{
    .filter{
      transform: translateX(0);
      &__bg{
        display: block;
      }
      &__close{
        display: flex;
      }
    }
  }

  .filter{
    border: none;
    border-radius: 0;
    @include menu;
    &__nav{
      @include grid100gap(.625em);
      padding: 1.56em 1.25em;
    }
    &__btn{
      text-transform: uppercase;
      height: 2.6875em;
      font-weight: 700;
    }
  }

  .filter-group{
    padding: 1.56em 1.25em;
  }

  .filter-toggle{
    display: flex;
    align-items: center;
    border-radius: 0.1875em;
    border: 1px solid $blue;
    padding: 0 1.56em;
    height: 3.125em;
    svg{
      width: 1.1875em;
      margin-right: .5em;
    }
    &__title{
      font-weight: 500;
      color: $blue;
    }
    &__count{
      margin-left: auto;
      color: #7E7E7E;
    }
  }

  .filter2{
    margin-top: 1.56em;
  }

  .sort{
    &__title{
      display: none;
    }
  }

  .selected-filters{
    margin-top: .9375em;
  }

  .selected-item{
    padding: 0 .625em;
    height: 1.9375em;
    span{
      font-size: .75em;
    }
  }

  .catalog-items{
    margin-top: 1.56em;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.56em 1.25em;
  }

  .manufacture-info{
    padding: .9375em;
    &__img{
      margin-left: .9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .nav-group{
    &__show{
      padding: .9375em 1.25em;
    }
    &__title{
      font-size: .875em;
      line-height: 136%;
    }
    &__hidden{
      padding: .9375em;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .filter2{
    margin-top: .94em;
    display: block;
  }

  .sort{
    margin-top: .94em;
    .fg{
      width: 100%;
    }
  }

  .selected-filters{
    margin-top: .625em;
    width: calc(100% + .9375em);
    .selected-item{
      margin: .9375em .9375em 0 0;
    }
  }

  .selected-item{
    &:not(._all){
      padding: 0;
      height: auto;
      border: none;
    }
    span{
      font-size: .75em;
    }
  }

  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }

  .manufacture-info{
    display: block;
    &__img{
      margin: .9375em 0 0;
    }
  }
}
