.grid-column2,
.grid-column3,
.grid-column4{
  display: grid;
  gap: 1.875em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .9375em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 1.875em * 3 / 4));
}


@media screen and (max-width: 992px){
  .grid-column-tablet,
  .grid-column-tablet2{
    gap: 1.25em;
  }

  .grid-column-tablet{
    grid-template-columns: 100%;
  }

  .grid-column-tablet2{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }
}


@media screen and (max-width: 576px){
  .grid-column-mob{
    gap: .9375em;
  }

  .grid-column-mob{
    grid-template-columns: 100%;
  }
}
