.favourite{
  padding: 1.56em 0 7.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__delete{
    display: flex;
    align-items: center;
    &:hover{
      .favourite__delete-title{
        opacity: 1;
      }
    }
    &-icon{
      @include center;
      width: 3em;
      height: 3em;
      border-radius: 0.1875em;
      background-color: $lightGray;
      flex-shrink: 0;
      margin-right: .94em;
      svg{
        width: 1.5em;
      }
    }
    &-title{
      transition: inherit;
      border-bottom: 1px dotted $black;
      opacity: .7;
    }
  }
  .category-nav{
    border: none;
    background-color: $lightGray;
    padding: 0;
  }
  &__link{
    padding: 1.06em 1.875em;
    display: block;
    font-size: 1em;
    line-height: 140%;
    font-weight: 500;
    span{
      color: #989EA1;
      font-size: .8125em;
      font-weight: 400;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $primary;
      span{
        color: rgba(255, 255, 255, 0.66);
      }
    }
  }
}

.favourite-available{
  border: $border;
  border-radius: .1875em;
}


@media screen and (max-width: 992px){
  .favourite{
    &__delete{
      &-icon{
        width: 2.1875em;
        height: 2.1875em;
        margin-right: .625em;
        svg{
          width: 1.1875em;
        }
      }
      &-title{
        font-size: .875em;
      }
    }
  }

  .favourite-available{
    border: none;
    .filter-group{
      padding: 0;
      display: flex;
      align-items: center;
      &__top .text18{
        font-size: 0.875em;
      }
      &__bottom{
        margin: 0 0 0 .94em;
      }
      &__checks{
        display: flex;
        align-items: center;
        gap: unset;
        .m-radio{
          margin: 0 .94em 0 0;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .favourite{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__delete{
      margin: 1em 0 0;
    }
    .category-toggle{
      margin-bottom: .94em;
    }
  }

  .favourite-available{
    border: none;
    .filter-group{
      display: block;
      &__bottom{
        margin: .375em 0 0 0;
      }
    }
  }
}
