@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 3.9375em;
    border-radius: 0.1875em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 0.8125em;
        line-height: 110%;
    }
}

.m-btn-red{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: #C33E35;
        color: #fff;
    }
    &:disabled{
        background: #816967;
        pointer-events: none;
        color: rgba(#fff,.5);
    }
}

.m-btn-yellow{
    background-color: $yellow;
    color: #342914;
    &:hover{
        background-color: $primary;
        color: rgba(#fff,.8);
    }
}

.arrow-btn{
    text-transform: uppercase;
    font-weight: 700;
    svg{
        width: 1.3125em;
        margin-left: .94em;
    }
    &:hover{
        path{
            fill: rgba(#fff,.8);
        }
    }
}

.m-btn-red-outline{
    border: 2px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-blue-outline{
    border: 1px solid $blue;
    color: $blue;
    &:hover{
        background-color: $blue;
        color: #fff;
    }
}

.m-btn-normal{
    text-transform: uppercase;
    font-weight: 700;
}

.m-btn-blue{
    background-color: $blue;
    color: #fff;
    &:hover{
        background-color: darken($blue,10%);
        color: #fff;
    }
}

.m-btn-dash{
    color: rgba($black,.7);
    span{
        border-bottom: 1px dashed rgba($black,.7);
        font-size: 1em;
        line-height: 140%;
        transition: inherit;
    }
    &:hover{
        color: $primary;
        span{
            border-color: $primary;
        }
    }
}
