.about2{
  @media screen and (min-width: 993px){
    .about__content{
      padding-top: 1.56em;
      padding-bottom: 4.875em;
    }
    .text{
      margin-top: 2.5em;
    }
  }
}

.special{
  padding: 3.125em 0 0;
  &__items{
    margin-top: 2.5em;
  }
  .text{
    margin-top: 3.125em;
  }
}

.special-item{
  display: flex;
  align-items: flex-start;
  &__num{
    font-weight: 500;
    color: $blue;
    margin-right: 1.25em;
    @include sameNumWidth;
    span{
      font-size: 3.1875em;
    }
  }
  &__content{
    @include grid100gap(.44em);
  }
}


@media screen and (max-width: 992px){
  .special{
    &__items{
      margin-top: 1.875em;
    }
    .text{
      p{
        font-size: .9375em;
        line-height: 173%;
      }
    }
  }

  .special-item{
    &__num{
      span{
        font-size: 2.5em;
      }
    }
    .text18{
      font-size: 1.0625em;
      line-height: 129%;
    }
    .text15{
      font-size: .875em;
      line-height: 143%;
    }
  }
}


@media screen and (max-width: 576px){
  .special{
    .text{
      p{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }

  .special-item{
    display: block;
    &__num{
      margin: 0 0 .25em;
      span{
        line-height: 142%;
      }
    }
  }
}
